.c-pagination {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;

    &__btn,
    &__btn-prev,
    &__btn-next {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 48px;
        color: $accent-secondary-text;
        background: $light-fill;
        font-size: $md-fs;
        text-decoration: none;
        cursor: pointer;
        border: none;
        outline: none;
        padding: 0;

        &:not(:last-child) {
            border-right: 1px solid $secondary-border;
        }

        &:hover,
        &:focus-visible {
            color: $primary-text;
            background: $info-steel-fill;
            text-decoration: none;
        }

        &:disabled,
        &.is-disabled {
            pointer-events: none;

            & > * {
                opacity: .4;
            }
        }

        &.is-active {
            font-weight: $font-weight-bold;
            color: $primary-text;
        }
    }

    .c-pagination__btn-prev,
    .c-pagination__btn-next {
        min-width: 64px;
    }

    &__btn {
        min-width: 48px;
    }

    &__btn-prev {
        border-radius: 6px 0 0 6px;
    }

    &__btn-next {
        border-radius: 0 6px 6px 0;
    }

    &--secondary {
        .c-pagination__btn,
        .c-pagination__btn-prev,
        .c-pagination__btn-next {
            color: $primary-text;
            background: none;

            &:not(:last-child) {
                border-right: 0;
            }

            &:hover,
            &:focus-visible {
                color: $info-text;
            }

            &.is-active {
                &:hover {
                    color: $primary-text;
                }
            }
        }

        .c-pagination__btn-prev,
        .c-pagination__btn-next {
            border-radius: 0;
        }
    }

    &--xs {
        .c-pagination__btn,
        .c-pagination__btn-prev,
        .c-pagination__btn-next {
            height: 21px;
            min-width: 21px;
            font-size: $sm-fs;
        }

        .c-pagination__btn-prev,
        .c-pagination__btn-next {
            .c-ico {
                margin-top: 1px;
            }
        }

        .c-pagination__btn-prev {
            margin-right: 4px;
        }

        .c-pagination__btn-next {
            margin-left: 4px;
        }
    }

    &--center {
        justify-content: center;
    }
}


