$gap-sizes: (
    "sm": 8,
    "md": 12
);

.c-list {
    margin: 0;
    margin-left: 20px;
    padding: 0;

    &__item {
        position: relative;
    }

    &__item-info {
        color: $success-text;
        font-size: $xs-fs;
        margin-left: 6px;
    }

    &--arrow-up,
    &--arrow-right-info,
    &--check-info {
        line-height: $lg-lh;

        .c-list__item {
            position: relative;
            list-style-type: none;

            &::before {
                content: '';
                position: absolute;
            }
        }
    }

    &--arrow-up {
        .c-list__item {
            &::before {
                top: 7px;
                left: -21px;
                @include bg-svg($arrow-up);
            }
        }
    }

    &--arrow-right-info {
        .c-list__item {
            padding-left: 6px;

            &::before {
                top: 5px;
                left: -21px;
                @include bg-svg($arrow-right-info);
            }
        }
    }

    &--check-info {
        .c-list__item {
            padding-left: 8px;

            &::before {
                top: 0;
                left: -24px;
                @include bg-svg($check-info);
            }
        }
    }

    @each $name, $value in $gap-sizes {
        &--gap-#{$name} li {
            margin-bottom: (#{$value}px);
        }
    }

    &--gap-sm,
    &--gap-md {
        li:last-child {
            margin-bottom: 0;
        }
    }

    &--animated {
        padding: 0;
        height: 36px;
        background: $light-fill;
        border-radius: 32px;
        box-shadow: $box-shadow-accent;
        overflow: hidden;

        li {
            display: flex;
            align-items: center;
            height: inherit;
            text-align: left;
            font-weight: $font-weight-bold;
            line-height: 1.2;
            padding: 9px 8px 8px 38px;
            top: 36px;
            animation: slideUpList 8s ease-in-out infinite;
        }

        .c-list__item::before {
            top: 12px;
            left: 20px;
        }
    }
}

@keyframes slideUpList {
    0% {
        top: calc(1 * 36px);
    }
    4%, 30% {
        top: 0;
    }
    35%, 63% {
        top: calc(-1 * 36px);
    }
    68%, 94% {
        top: calc(-2 * 36px);
    }
    99.4% {
        top: calc(-3 * 36px);
        opacity: 1;
    }
    99.5% {
        opacity: 0;
    }
    100% {
        top: calc(1 * 36px);
        opacity: 0;
    }
}


