.c-loader {
    position: relative;
    display: inline-block;
    width: 96px;
    height: 54px;
    border-radius: 30px;
    background: $light-fill;
    box-shadow: $box-shadow-default;

    &__indicator,
    &__indicator::before,
    &__indicator::after {
        position: absolute;
        display: inline-block;
        border-radius: 50%;
        animation: loader-loading 0.75s infinite cubic-bezier(0.13, 1.29, 0.32, 1.29);
    }

    &__indicator {
        @include center-position;
        width: 14px;
        height: 14px;
        animation-delay: 0.05s;

        &::before,
        &::after {
            content: '';
            @include center-vertical-position;
        }

        &::before {
            left: -19px;
            width: 12px;
            height: 12px;
        }

        &::after {
            left: 19px;
            width: 16px;
            height: 16px;
            animation-delay: 0.10s;
        }
    }
}

@keyframes loader-loading {
    0%, 100% {
        background: transparent;
    }
    50%  {
        background: $info-fill;
    }
}


