.c-option-btn {
    position: relative;
    display: flex;
    flex-basis: content;
    width: 100%;
    align-items: center;
    justify-content: center;
    @extend .c-option-btn--xl;
    color: $primary-text;
    font-size: $md-fs;
    line-height: $md-lh;
    border-radius: 6px;
    border: 1px solid $service-border;
    outline: 1px solid transparent;
    outline-offset: -2px;
    background-color: $light-fill;
    box-shadow: $box-shadow-default;

    &:not(:disabled),
    &:not(.is-disabled) {
        @include hover-focus {
            border-color: $info-border;
        };

        &.is-active,
        &:active {
            box-shadow: none;

            & > .c-option-btn__text {
                font-weight: $font-weight-regular;
            }
        }

        &:active {
            border-color: $primary-border;
        }

        &:active:not(.c-option-btn--rounded),
        &.is-active {
            font-weight: $font-weight-bold;
            @include border-outline-color($primary-border);
        }
    }

    &:disabled,
    &.is-disabled {
        color: $accent-secondary-text;
        border-color: $secondary-light-border;
        outline-color: transparent;
        pointer-events: none;
        box-shadow: none;
    }

    &__text {
        font-size: 18px;
        line-height: 1.334;
    }

    &--base-width,
    &--massive {
        flex-basis: 199px;
    }

    &--massive,
    &--massive-md,
    &--massive-lg {
        min-height: 96px;
    }

    &--massive-md {
        flex-basis: 280px;
    }

    &--massive-lg {
        flex-basis: 314px;
    }



    &--xl {
        padding: 15px 23px;
    }

    &--xl-wide {
        padding-left: 40px;
        padding-right: 40px;
    }

    &--rounded {
        padding: 7px 23px;
        border-radius: 30px;
    }

    &--circle,
    &--circle-sm {
        width: 48px;
        height: 48px;
        flex-basis: auto;
        padding: 5px;
        border-radius: 50%;

        &.c-option-btn--placeholder {
            padding-left: 5px;

            &::before {
                @include center-position;
            }
        }
    }

    &--circle-sm {
        width: 40px;
        height: 40px;
    }

    &--check-view,
    &--check-view-highlight {
        padding-left: 54px;

        &::before {
            position: absolute;
            display: inline-block;
            content: '';
            left: 18px;
            @include bg-svg($plus);
        }

        &:not(:disabled),
        &:not(.is-disabled) {
            &:active,
            &.is-active{
                &::before {
                    @include bg-svg($check);
                }
            }

            &.is-active {
                &:hover {
                    &::before {
                        @include bg-svg($close);
                    }
                }
            }
        }

        &:disabled,
        &.is-disabled {
            &::before {
                @include bg-svg($plus-secondary);
            }
        }
    }

    &--check-view-highlight,
    &--arrow-right {
        &::after {
            position: absolute;
            display: inline-block;
            content: '';
            @include bg-svg($star-circle-warning);
        }
    }

    &--check-view-highlight {
        padding-right: 42px;

        &::after {
            right: 12px;
            @include bg-svg($star-circle-warning);
        }

        &:disabled,
        &.is-disabled {
            &::after {
                @include bg-svg($star-circle-secondary);
            }
        }
    }

    &--arrow-right {
        padding-right: 58px;

        &::after {
            right: 25px;
            @include bg-svg($arrow-right);
        }

        &:disabled,
        &.is-disabled {
            &::after {
                @include bg-svg($arrow-right-secondary);
            }
        }
    }

    &--placeholder {
        padding-left: 54px;
        color: $accent-secondary-text;
        border-style: dashed;
        pointer-events: none;
        box-shadow: none;

        &::before {
            position: absolute;
            display: inline-block;
            content: '';
            left: 18px;
            @include bg-svg($plus-secondary);
        }
    }
}


