.c-edit-avatar {
    cursor: pointer;

    &__container {
        position: relative;
        width: 176px;
        height: 176px;
        border: 1px solid $service-border;
        margin-bottom: 16px;

        &::before {
            position: absolute;
            content: '';
            display: block;
            @include full-position;
        }

        @include hover-focus {
            border-color: $info-border;

            &::before {
                background: $info-fill;
                opacity: .5;
            }

            .c-edit-avatar__text {
                display: block;
            }
        };

        img {
            display: block;
            width: 100%;
            height: auto;
        }
    }

    .c-link,
    &__text {
        font-size: $sm-fs;
        font-weight: $font-weight-bold;
    }

    &__text {
        display: none;
        width: 100%;
        position: absolute;
        @include center-vertical-position;
        text-align: center;
        color: $light-text;
        padding: 8px;
    }
}



