$ico-custom: (
    "fb": $fb-logo,
    "google": $google-logo
);

.c-social-ico {
    display: inline-block;
    vertical-align: middle;
    font-size: 0;

    &::before {
        content: '';
        display: block;
    }

    @each $name, $ico in $ico-custom {
        &--#{$name}::before {
            @include bg-svg($ico);
        }
    }
}


