.c-card-btn {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    flex-basis: 314px;
    width: 100%;
    color: $primary-text;
    font-size: $md-fs;
    line-height: $md-lh;
    text-align: left;
    padding: 23px;
    background-color: $light-fill;
    border-radius: 6px;
    border: 1px solid $service-border;
    outline: 1px solid transparent;
    outline-offset: -2px;
    box-shadow: $box-shadow-default;
    cursor: pointer;

    & > * {
        width: 100%;
    }

    @include hover-focus {
        border-color: $info-border;
    };

    &:active,
    &.is-active {
        box-shadow: none;
    }

    &:active {
        border-color: $primary-border;
    }

    &.is-active {
        @include border-outline-color($primary-border);
    }

    &__head,
    &__title {
        font-weight: $font-weight-bold;
    }

    &__head {
        color: $accent-secondary-text;
        font-size: $xs-fs;
        text-transform: uppercase;
        margin-bottom: 16px;
    }

    &__title {
        @extend .c-h3;
        margin-bottom: 14px;
    }

    &__content {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        width: inherit;
    }

    &__item {
        margin-right: 14px;

        &:last-child {
            margin-right: 0;

            &:not(:first-child) {
                font-weight: $font-weight-bold;
            }
        }
    }

    &--sm {
        padding-top: 16px;
        padding-bottom: 16px;

        .c-card-btn__title {
            @extend .c-h4;
            margin-bottom: 6px;
        }
    }

    &--icon-decorated {
        position: relative;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        padding-right: 72px;

        & > * {
            width: auto;
        }

        &::after {
            position: absolute;
            display: inline-block;
            content: '';
            @include center-vertical-position;
            right: 23px;
            @include bg-svg($pencil-xl);
        }

        .c-card-btn__title {
            @extend .c-h4;
            margin: 0 32px 0 0;

            &:last-child {
                margin-right: 0;
            }
        }

        .c-card-btn__text {
            position: relative;
            color: $info-text;
            padding-left: 24px;

            &::after {
                position: absolute;
                display: inline-block;
                content: '';
                @include center-vertical-position;
                left: 0;
                @include bg-svg($alert-rhombus-info);
            }
        }
    }
}


