.c-banner-card {
    position: relative;
    width: 474px;
    height: 557px;
    z-index: 1;

    &__image-main {
        margin: -16px 0 0 -70px;
        @extend .c-image--full;
        position: relative;
        z-index: -1;
    }

    &__label,
    &__note,
    &__image-colors,
    &__image-panel,
    &__tag,
    &__tag::before {
        position: absolute;
    }

    &__image-colors {
        display: block;
        right: 29px;
        top: 143px;
    }

    &__image-panel {
        display: block;
        right: 62px;
        bottom: 17px;
    }

    &__label {
        top: 12px;
        right: 75px;
        display: flex;
        align-items: center;
        max-width: 173px;
        padding: 12px 6px 12px 16px;
        color: $light-text;
        border-radius: 35px;
        border: 1px solid $secondary-border;
        background: $success-fill;
        @extend .u-text-bold;
        animation: moveInCircle 8s infinite linear;
        cursor: pointer;

        .c-ico {
            width: 35px;
            height: 35px;
            margin-right: 6px;
        }

        &:hover,
        &:focus {
            text-decoration: none;
        }
    }

    &__note {
        display: block;
        width: 158px;
        height: 158px;
        padding: 45px 38px;
        text-align: center;
        position: absolute;
        top: 205px;
        left: -92px;
        background: {
            image: url("%=static=%general/ai-bg.svg");
            repeat: no-repeat;
            size: contain;
        }
    }

    &__note-text {
        @extend .u-text-bold;
        @extend .u-text-sm;
        @extend .u-text-double-line;
    }

    &__tag {
        bottom: 172px;
        left: -77px;
        display: flex;
        align-items: center;
        margin-right: 13px;
        padding: 9px 0 9px 12px;
        color: $warning-deep-text;
        font-weight: $font-weight-bold;
        background: $warning-steel-fill;
        border-radius: 6px;
        border-top: 1px solid $secondary-border;
        border-bottom: 1px solid $secondary-border;
        border-left: 1px solid $secondary-border;
        box-shadow: $box-shadow-sm;

        &::before {
            content: "";
            display: inline-block;
            top: 50%;
            right: 0;
            width: 34px;
            height: 34px;
            border-radius: 6px;
            border: 1px solid $secondary-border;
            background: $warning-steel-fill;
            margin-right: -13px;
            transform: translateY(-50%) rotate(45deg);
            z-index: -1;
        }

        .c-ico {
            margin-left: 8px;
        }
    }

    &--xs {
        width: 394px;
        height: 503px;

        .c-banner-card__note {
            top: -17px;
            left: -173px;
        }

        &.c-banner-card--img-position {
            .c-banner-card__image-main {
                margin: 15px 0 0 -97px;
            }

            .c-banner-card__image-panel {
                right: -16px;
                top: 92px;
                bottom: auto;
            }

            .c-banner-card__image-colors {
                top: auto;
                bottom: -39px;
                left: -193px;
                right: auto;
            }
        }
    }

    &--sm {
        width: 436px;
        height: 543px;

        .c-banner-card__note {
            top: 117px;
            left: -152px;
        }

        .c-banner-card__label {
            top: 31px;
            right: 75px;
        }

        &.c-banner-card--img-position {
            .c-banner-card__image-main {
                margin: -35px 0 0 -118px
            }

            .c-banner-card__image-panel {
                right: 86px;
                bottom: 15px;
            }

            .c-banner-card__image-colors {
                right: 5px;
                top: 165px;
            }
        }
    }

    &--md {
        width: 551px;
        height: 576px;

        .c-banner-card__note {
            top: auto;
            bottom: -39px;
            left: auto;
            right: 5px;
        }

        &.c-banner-card--img-position {
            .c-banner-card__image-main {
                margin: -42px 0 0 2px;
            }
        }
    }
}

@keyframes moveInCircle {
    0% {
        transform: rotate(360deg) translate(15px) rotate(-360deg);
    }
    100% {
        transform: rotate(0deg) translate(15px) rotate(0deg);
    }
}

