.c-rounded-box {
    padding: 40px;
    border-radius: 6px;
    background: $light-fill;

    &__top {
        padding: 32px;
        border-top-left-radius: inherit;
        border-top-right-radius: inherit;
    }

    &__bottom {
        padding: 24px 32px;
        color: $light-text;
        background: $accent-secondary-fill;
        border-bottom-left-radius: inherit;
        border-bottom-right-radius: inherit;
    }

    &__head,
    &__footer {
        padding: 16px;
    }

    &__footer {
        border-top: 1px solid $secondary-border;
        padding-top: 8px;
        padding-bottom: 8px;
    }

    &--radius-md {
        border-radius: 8px;
    }

    &--secondary {
        background: $secondary-fill;
    }

    &--info {
        background: $info-light-fill;
    }

    &--info-light {
        background: $info-steel-fill;
    }

    &--none-padding {
        padding: 0;
    }

    &--xs,
    &--xs-shot {
        padding: 24px;
    }

    &--xs-shot {
        padding-top: 16px;
        padding-bottom: 16px;
    }

    &--sm {
        padding: 26px 55px;
    }

    &--md,
    &--md-slim {
        padding: 32px 105px;
    }

    &--md-slim {
        padding-left: 32px;
        padding-right: 32px;
    }

    &--lg {
        padding: 40px 32px;
    }

    &--xl,
    &--xl-narrow {
        padding: 64px 134px;
    }

    &--xl-narrow {
        padding: 64px;
    }

    &--border-success {
        border: 3px solid $success-light-border;
    }

    &--decor-arrow-left,
    &--decor-arrow-top {
        position: relative;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, .15);

        &:before,
        &:after {
            content: '';
            display: block;
            position: absolute;
            @include center-vertical-position;
            left: -38px;
            border: 18px solid transparent;
        }

        &:before {
            border-right: 18px solid $secondary-light-border;
        }

        &:after {
            border-right: 19px solid $light-border;
        }
    }

    &--decor-arrow-left {
        margin-left: 18px;
    }

    &--decor-arrow-top {
        margin-top: 18px;

        &:before,
        &:after {
            top: -19px;
            left: 26px;
            border: 18px solid transparent;
        }

        &:before {
            border-bottom: 18px solid $secondary-light-border;
        }

        &:after {
            border-bottom: 18px solid $light-border;
        }
    }
}


