.w-page {
    display: flex;
    flex-direction: column;
    min-height: 100%;

    &__content {
        flex: 1;
        width: 100%;
        display: flex;
        flex-direction: column;
    }

    &--fill {
        background: $light-fill;
    }

    &--fill-secondary {
        background: $help-light-fill;
    }
}

.w-main {
    display: flex;
    flex-direction: column;
    flex: 1;

    &__content {
        flex: 1;
    }

    &__bottom {
        border-top: 1px solid $secondary-border;

        &--sticky {
            position: sticky;
            bottom: 0;
            left: 0;
            right: 0;
            z-index: 10;
            background: $light-fill;
        }
    }

    &--extra-short .w-main__content,
    &__content {
        padding: 40px 0 64px;
    }

    &--extra-short .w-main__content-head,
    &__content-head {
        margin-bottom: 72px;
    }

    &--extra-short .w-main__content-container:first-child,
    &__content-container:first-child {
        margin-top: 80px;
    }

    &--extra-short .w-main__bottom,
    &__bottom {
        margin-bottom: 60px;
        padding: 16px 0;
    }

    &--short {
        .w-main__content-container:first-child {
            margin-top: 40px;
        }
    }

    &--long,
    &--extra-long {
        .w-main__content-container:first-child {
            margin-top: 0;
        }
    }

    &--extra-long {
        .w-main__content {
            padding-top: 24px;

        }
    }

    &--compressed {
        flex: 0;

        .w-main__content {
            flex: 0;
            padding: 24px 0 84px;
        }

        .w-main__content-head {
            margin-bottom: 8px;
        }

        .w-main__content-container {
            &:first-child {
                margin-top: 56px;
            }
        }

        .w-main__bottom {
            padding: 24px 0;
        }
    }
}

.w-container {
    padding: 40px 0 88px;

    &--tiny {
        padding: 24px 88px;
    }
}

$width: (
    "xxs": $page-width-xxs,
    "xs": $page-width-xs,
    "sm": $page-width-sm,
    "sm-wide": $page-width-sm-wide
);

.w-centered {
    margin-left: auto;
    margin-right: auto;
    width: $page-width + 32px;
    max-width: 100%;
    padding-left: 16px;
    padding-right: 16px;

    @each $size, $value in $width {
        &--#{$size} {
            width: $value + 32px;
        }
    }
}

// BEGIN GAPS
$gaps: (
    "xs": $xs-gap,
    "sm": $sm-gap,
    "md": $md-gap,
    "lg": $lg-gap,
    "xl": $xl-gap,
    "2xl": $_2xl-gap,
    "3xl": $_3xl-gap,
    "4xl": $_4xl-gap,
    "5xl": $_5xl-gap,
    "6xl": $_6xl-gap,
    "7xl": $_7xl-gap,
    "8xl": $_8xl-gap,
    "extra": $extra-gap
);

@each $size, $value in $gaps {
    .w-gap-#{$size} {
        margin-bottom: $value;
    }
}

.w-gap-xs,
.w-gap-sm,
.w-gap-md,
.w-gap-lg,
.w-gap-xl,
.w-gap-2xl,
.w-gap-3xl,
.w-gap-4xl,
.w-gap-5xl,
.w-gap-6xl,
.w-gap-7xl,
.w-gap-8xl,
.w-gap-extra {
    &:last-child {
        margin-bottom: 0;
    }
}
// END GAPS

// BEGIN COLUMN GAPS
@mixin column-gap($column-gap) {
    & > * {
        margin-bottom: $column-gap;
    }
}

$column-gaps: (
    "xxs": $xs-gap,
    "xs": $xs-gap + 2px,
    "sm": $sm-gap,
    "md": $md-gap,
    "lg": $lg-gap,
    "xl": $xl-gap
);

@each $size, $value in $column-gaps {
    .c-column-gap-#{$size} {
        @include column-gap($value);
    }
}

.c-column-gap-xxs,
.c-column-gap-xs,
.c-column-gap-sm,
.c-column-gap-md,
.c-column-gap-lg,
.c-column-gap-xl {
    & > * {
        &:last-child {
            margin-bottom: 0;
        }
    }
}
// END COLUMN GAPS


