$utilities: (
    "text-weight": (font-weight, "u-text-"),
    "text-align": (text-align, "u-text-"),
    "text-colors": (color, "u-text-"),
    "text-sizes": (font-size, "u-text-"),
    "line-height": (line-height, "u-lh-"),
    "display-types": (display, "u-"),
    "margin-top": (margin-top, "u-mt-"),
    "margin-bottom": (margin-bottom, "u-mb-"),
    "margin-left": (margin-left, "u-ml-"),
    "margin-right": (margin-right, "u-mr-"),
    "text-lines": (-webkit-line-clamp, "u-text-"),
    "text-white-space": (white-space, "u-"),
    "pointer-events": (pointer-events, "no-"),
    "white-space": (white-space, "u-ws-")
);

$values: (
    "text-weight": (
        "normal": $font-weight-regular,
        "bold": $font-weight-bold
    ),
    "text-align": (
        "left": left,
        "right": right,
        "center": center
    ),
    "text-colors": (
        "primary": $primary-text,
        "info": $info-text,
        "help": $accent-secondary-text,
        "light": $light-text,
        "success": $success-text
    ),
    "text-sizes": (
        "10": $_2xs-fs,
        "xs": $xs-fs,
        "sm": $sm-fs,
        "md": $md-fs,
        "lg": $lg-fs
    ),
    "line-height": (
        "sm": $sm-lh,
        "md": $md-lh
    ),
    "text-lines": (
        "single-line": 1,
        "double-line": 2,
        "3-line": 3
    ),
    "text-white-space": (
        "nowrap": nowrap
    ),
    "display-types": (
        "hidden": none
    ),
    "margin-top": (
        "0": 0,
        "3": 3px,
        "4": 4px,
        "10": 10px,
        "16": 16px,
        "17": 17px,
        "40": 40px,
        "2-neg": -2px,
        "3-neg": -3px,
        "8-neg": -8px,
        "24-neg": -24px,
        "27-neg": -27px,
        "66-neg": -66px
    ),
    "margin-bottom": (
        "0": 0,
        "16": 16px
    ),
    "margin-left": (
        "40": 40px,
        "48": 48px,
        "103": 103px,
        "6-neg": -6px,
        "8-neg": -8px
    ),
    "margin-right": (
        "4": 4px,
        "6": 6px,
        "8": 8px,
        "16": 16px,
        "48": 48px,
        "8-neg": -8px,
        "20-neg": -20px
    ),
    "pointer-events": (
        "pointer-events": none
    ),
    "white-space": (
        "nowrap": nowrap
    )
);

@each $category, $property in $utilities {
    $css-property: nth($property, 1);
    $prefix: nth($property, 2);

    @each $key, $value in map-get($values, $category) {
        .#{$prefix}#{$key} {
            @if $category == "text-lines" {
                @include text-line {#{$css-property}: $value;};
            } @else {
                #{$css-property}: $value !important;
            }

        }
    }
}

.u-scrollbar-secondary {
    @include scrollbar-decor($secondary-dark-fill, 7px, 6px);
}





