.c-image-container {
    cursor: pointer;
    width: 384px;

    &:hover {
        .c-image-container__img {
            transform: translateY(-10px);
            box-shadow: $box-shadow-5xl;
        }

        .c-image-container__btn {
            display: flex;
        }
    }

    &__img {
        position: relative;
        border-radius: 6px;
        overflow: hidden;
        transition: transform 0.3s ease, box-shadow 0.3s ease;
        box-shadow: $box-shadow-default;
        border: 1px solid transparent;
        margin-bottom: 16px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__btn {
        display: none;
        position: absolute;
        @include center-position;
        width: 221px;
    }

    .c-image-container__zoom {
        position: absolute;
        right: 24px;
        bottom: 24px;
    }

    &__title {
        text-align: center;
        font-weight: $font-weight-bold;
    }

    &__description {
        margin-top: 8px;
    }

    &--sm {
        width: 280px;
    }

    &--xs {
        width: 176px;

        .c-image-container__img {
            width: 176px;
        }

        .c-image-container__zoom {
            right: 10px;
            bottom: 10px;
        }
    }

    &--secondary-content {
        color: $accent-secondary-text;
        font-size: $sm-fs;

        &:hover {
            .c-image-container__title,
            .c-image-container__description {
                color: $primary-text;
            }
        }

        .c-image-container__title {
            text-align: left;
        }
    }

    &--hover-outline {
        &:hover {
            .c-image-container__img {
                border-color: $info-border;
            }
        }
    }
}


