.c-label {
    display: inline-block;
    padding: 2px 8px;
    font-size: $xs-fs;
    font-weight: $font-weight-bold;
    color: $primary-text;
    background: $success-dark-fill;
    border-radius: 8px;

    &--warning {
        background: $warning-secondary-fill;
    }

    &--highlight {
        background: $warning-steel-secondary-fill;
        color: $warning-deep-secondary-text;
    }

    &--help {
        background: $help-dark-fill;
        color: $success-dark-text;
    }

    &--info,
    &--accent,
    &--accent-light {
        color: $light-text;
    }

    &--info {
        background: $info-dark-fill;
    }

    &--accent {
        background: $accent-secondary-fill;
    }

    &--accent-light {
        background: $accent-steel-fill;
    }

    &--secondary {
        background: $secondary-fill;
        color: $accent-secondary-text;
    }

    &--md {
        padding: 4px 10px;
        font-size: $sm-fs;
    }

    &--lg {
        padding: 10px 16px;
        font-size: $md-fs;
        border-radius: 48px;
    }
}


