.c-toggle-color {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    cursor: pointer;

    &:hover,
    &:focus {
        .c-toggle-color__input:not(:checked) ~ .c-toggle-color__circle {
            box-shadow: none;

            &::before {
                border-color: $info-border;
                opacity: 1;
            }
        }
    }

    &__input {
        @include invisible-input;

        &:checked ~ .c-toggle-color__circle {
            box-shadow: none;

            &::before {
                border-width: 2px;
                border-color: $primary-border;
                opacity: 1;
            }
        }
    }

    &__circle {
        position: relative;
        display: block;
        width: 36px;
        height: 36px;
        border: 1px solid transparent;
        border-radius: 50%;
        box-shadow: $box-shadow-opacity-md;

        &::before {
            content: '';
            position: absolute;
            top: -7px;
            left: -7px;
            width: 48px;
            height: 48px;
            border: 1px solid transparent;
            transition: opacity 0.3s ease, transform 0.12s;
            border-radius: 50%;
            opacity: 0;
        }
    }

    &--default {
        .c-toggle-color__circle {
            border: 1px solid $service-border;

            &::after {
                content: '';
                position: absolute;
                width: 2px;
                height: 18px;
                top: 50%;
                left: 50%;
                background: $primary-text;
                transform: translate(-50%, -50%) rotate(24deg);
            }
        }
    }

    &--sm,
    &--sm .c-toggle-color__circle::before {
        width: 40px;
        height: 40px;
    }
    &--sm {
        .c-toggle-color__circle {
            width: 30px;
            height: 30px;

            &::before {
                top: -6px;
                left: -6px;
            }
        }
    }
}


