.c-large-value {
    max-width: 263px;
    width: 100%;
    padding: 22px;

    &__container {
        position: relative;

        &::before {
            display: inline-block;
            width: 1px;
            content: '';
            position: absolute;
            top: 22px;
            bottom: 0;
            left: -22px;
            background: $primary-fill;
        }
    }

    &__value,
    &__title {
        color: $info-text;
        @extend .u-text-bold;
    }

    &__value {
        font-size: 72px;
        line-height: $sm-lh;

        span {
            font-size: $_2xl-fs;
            margin-left: 16px;
        }
    }

    &__title {
        font-size: 24px;
        margin-bottom: 64px;
    }

    &__text {
        max-width: 184px;
        font-size: $lg-fs;
    }

    &--sm {
        max-width: 224px;
        padding: 0;

        .c-large-value__title {
            font-size: $lg-fs;
            margin-bottom: 16px;
        }

        .c-large-value__text {
            font-size: $md-fs;
            max-width: 224px;
        }
    }

    &--no-line {
        .c-large-value__container::before {
            display: none;
        }
    }

    &--title-secondary {
        .c-large-value__title {
            color: $primary-text;
        }
    }
}


