.form-control {
    display: block;
    width: 100%;
    outline: none;
    background: $light-fill;
    border: 1px solid $service-border;
    padding-left: 16px;
    padding-right: 16px;
    color: $primary-text;
    font-size: $md-fs;
    @include input-size(56px);
    @include placeholder {
        font-size: $md-fs;
        color: $accent-secondary-text;
    }
    @include input-type-number;

    &:not(:disabled) {
        &:hover {
            border: 1px solid $info-border;
        }

        &:active,
        &:focus {
            border: 1px solid $primary-border;

            &::placeholder {
                font-size: 0;
            }
        }
    }

    &:disabled {
        border-color: $secondary-border;
        background-color: $secondary-fill;
        pointer-events: none;
    }
}

textarea.form-control {
    padding: 16px;
    height: auto;
    line-height: $md-lh;
    resize: none;
}

select.form-control {
    padding-right: 32px;
    color: $primary-text;
    appearance: none;
    transition: none;
    background: url('%=static=%general/arrow-down.svg') no-repeat right 7px center $light-fill;

    option {
        color: $primary-text;
    }
}


