html, body {
    height: 100%;
}
html {
    &.no-scroll {
        overflow: hidden;
    }
}

*, *::before, *::after {
    box-sizing: border-box;
}

body {
    min-width: $page-width;
    background-color: $secondary-fill;
}

@import "typography";
@import "wrappers";
@import "grid";
@import "utilities";
@import "plugins/_autocomplete.scss";
@import "plugins/_bootstrap.scss";
@import "plugins/_range-slider.scss";
@import "plugins/_draft-editor.scss";
@import "plugins/_swiper-bundle.scss";

button, a {
    cursor: pointer;
}

.c-section-table {
    width: 100%;
    border-collapse: collapse;

    &__row {
        &:first-child td {
            padding-top: 0;
            border-top: 0;
        }

        td {
            padding: 16px 16px 16px 0;
            border-top: 1px solid $secondary-border;
            border-bottom: 1px solid $secondary-border;
            height: 80px;

            &:first-child {
                width: 33%;
                font-weight: $font-weight-bold;
            }

            &:last-child {
                padding-right: 0;
            }
        }
    }

    &--column-wide {
        td:first-child {
            width: 46%;
        }
    }
}

.c-separator-line {
    display: block;
    width: 1px;
    height: 32px;
    background: $secondary-dark-fill;

    &--xs {
        height: 16px;
    }

    &--sm {
        height: 20px;
    }

    &--xl {
        height: 70px;
    }
}

.c-form-group {
    position: relative;

    &__message {
        display: none;
        font-size: $sm-fs;
        text-align: left;
        margin-top: 9px;

        &:empty {
            margin: 0;
        }
    }

    &.has-success,
    &.has-error {
        .c-addon-field__help-control {
            display: block;
        }
    }

    &.has-success {
        .c-addon-field__help-control {
            color: $info-text;
        }
    }

    &.has-error {
        .c-text-field {
            @include border-outline-color($danger-border);
        }

        .c-form-group__message {
            display: block;
            color: $danger-text;
            font-weight: $font-weight-bold;
        }

        .c-addon-field__help-control.c-ico {
            color: $danger-text;
        }
    }
}

.c-text-field {
    display: block;
    width: 100%;
    outline: 1px solid transparent;
    outline-offset: -2px;
    background: $light-fill;
    border: 1px solid $service-border;
    padding-left: 16px;
    padding-right: 16px;
    color: $primary-text;
    font-size: $md-fs;
    @include input-size(56px);
    @include placeholder {
        font-size: $md-fs;
        color: $accent-secondary-text;
    }
    @include input-type-number;

    &:hover {
        border: 1px solid $info-border;
    }

    &:active,
    &:focus {
        border: 1px solid $primary-border;
    }

    &.is-disabled,
    &:disabled {
        border-color: $secondary-border;
        background: $secondary-fill;
        pointer-events: none;
    }
}

textarea.c-text-field {
    padding: 24px;
    height: auto;
    line-height: $md-lh;
    resize: none;
}


