.c-dropdown-container {
    display: none;
    position: absolute;
    top: auto;
    bottom: 100%;
    left: 0;
    right: 0;
    transform: translateY(-1px);
    background: $light-fill;
    padding: 32px;
    box-shadow: $box-shadow-reverse;
    overflow: hidden;
    z-index: 11;

    &__inner {
        position: relative;
        @extend .w-centered;
    }

    &__reset,
    &__close {
        position: absolute;
    }

    &__close {
        top: -28px;
        right: 8px;
    }

    &__reset {
        display: inline-flex;
        align-items: center;
        top: -16px;
        right: 56px;
        @extend .u-text-sm;
    }

    &.is-open {
        display: block;
    }
}


