.c-collapse-card {
    border: 1px solid $service-border;
    border-radius: 6px;
    box-shadow: $box-shadow-default;
    overflow: hidden;

    @include hover-focus {
        border-color: $info-border;
    };

    &__toggle {
        position: relative;
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        color: $primary-text;
        font-size: $md-fs;
        line-height: $md-lh;
        background: $light-fill;
        border: none;
        outline: none;
        padding: 23px 72px 23px 23px;
        border-radius: 6px 6px 0 0;

        &::after {
            display: none;
            position: absolute;
            @include center-vertical-position;
            right: 23px;
            content: '';
            @include bg-svg($pencil-xl);
        }

        &:hover,
        &:focus {
            text-decoration: none;
            color: $primary-text;
        }

        &.collapsed {
            border-radius: 6px;

            &::after,
            .c-collapse-card__data {
                display: block;
            }
        }

        .c-ico {
            display: none;
        }
    }

    &__head {
        padding: 23px;

        .c-collapse-card__toggle {
            position: relative;
            display: inline-block;
            width: auto;
            padding: 0;
            border-radius: 0;

            &:hover {
                color: $info-text;
            }

            &::after {
                right: -32px;
            }
        }
    }

    &__title {
        font-weight: $font-weight-bold;
    }

    &__data {
        display: none;
        position: relative;

        &::after {
            display: none;
            position: absolute;
            @include center-vertical-position;
            left: 0;
            content: '';
            @include bg-svg($alert-rhombus-info);
        }

        &.is-selected {
            color: $info-text;
            padding-left: 24px;

            &::after {
                display: block;
            }
        }
    }

    &__dialog {
        position: relative;
        z-index: 1;
        background: $secondary-fill;
    }

    &__inner {
        padding: 32px 104px 64px;
    }

    &__inner-sm {
        padding: 8px 23px 23px;
    }

    &__inner-xs {
        padding: 0 16px 16px;
    }

    &--light {
        & > .c-collapse-card__toggle,
        & > .c-collapse-card__head .c-collapse-card__toggle,
        & > .c-collapse-card__dialog {
            background: $light-fill;
        }
    }

    &--secondary {
        & > .c-collapse-card__toggle,
        & > .c-collapse-card__head,
        & > .c-collapse-card__head .c-collapse-card__toggle,
        & > .c-collapse-card__dialog {
            background: $secondary-fill;
        }
    }

    &--compact {
        .c-collapse-card__toggle {
            padding: 16px;
            transition: margin-bottom .25s;
            margin-bottom: -8px;

            &.collapsed {
                margin-bottom: 0;
            }

            .c-collapse-card__title {
                font-weight: $font-weight-regular;
            }
        }
    }

    &--overflow-visible {
        overflow: visible;

        & > .c-collapse-card__toggle,
        & > .c-collapse-card__head {
            border-radius: 6px;
        }

        & > .c-collapse-card__dialog {
            border-bottom-left-radius: 6px;
            border-bottom-right-radius: 6px;
        }
    }

    &--icon-chevron {
        .c-collapse-card__head .c-collapse-card__toggle {
            padding-right: 0;
            padding-left: 32px;

            .c-ico {
                left: 0;
                right: auto;
            }
        }

        .c-collapse-card__toggle {
            padding-right: 46px;

            &::after {
                display: none;
            }

            &.collapsed {
                .c-ico {
                    transform: translateY(-50%) rotate(0);
                }
            }

            .c-ico {
                display: block;
                position: absolute;
                top: 50%;
                transform: translateY(-50%) rotate(-180deg);
                right: 11px;
                transition: all .2s linear;
            }
        }
    }
}


