@mixin selection {
    &::-moz-selection {
        @content;
    }

    &::selection {
        @content;
    }
}

@mixin placeholder {
    &::-webkit-input-placeholder {
        @content;
    }
    &:-moz-placeholder {
        @content;
    }
    &::-moz-placeholder {
        @content;
    }
    &:-ms-input-placeholder {
        @content;
    }
}

@mixin input-type-number {
    &[type="number"]::-webkit-outer-spin-button,
    &[type="number"]::-webkit-inner-spin-button {
        -webkit-appearance: none;
    }

    &[type='number'],
    &[type="number"]:hover,
    &[type="number"]:focus {
        appearance: none;
        -moz-appearance: textfield;
    }
}

@mixin invisible-input {
    position: absolute;
    left: -9999999999px;
    width: 0;
    height: 0;
    opacity: 0;
    z-index: -1;
}

@mixin input-size($size) {
    height: $size;
    line-height: $size;
}

@mixin reset-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

@mixin hover-focus {
    &:hover,
    &:focus,
    &:focus-visible {
        text-decoration: none;
        @content;
    }
}

@mixin full-position {
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

@mixin center-position {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@mixin center-vertical-position {
    top: 50%;
    transform: translateY(-50%);
}

@mixin center-horizontal-position {
    left: 50%;
    transform: translateX(-50%);
}

@mixin border-outline-color($color) {
    outline-color: $color;
    border-color: $color;
}

@mixin scrollbar-decor($color, $width, $radius) {
    @-moz-document url-prefix() {
        scrollbar-width: thin;
        scrollbar-color: $color transparent;
    }

    &::-webkit-scrollbar {
        width: $width;
    }

    &::-webkit-scrollbar-thumb {
        width: $width;
        border-radius: $radius;
        background-color: $color;
    }

    &::-webkit-scrollbar-track {
        border-radius: $radius;
    }
}

@mixin text-line {
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    word-wrap: break-word;
    word-break: break-word;
    @content;
}

@mixin user-select-none {
    -webkit-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
