.c-modal-common,
.c-modal-splash,
.c-modal-bottom {
    display: none;
    position: fixed;
    z-index: 1050;

    &__dialog {
        position: relative;
        outline: none;
    }
}

.c-modal-common,
.c-modal-bottom {
    &__dialog {
        max-width: 100%;
        margin-left: auto;
        margin-right: auto;
    }

    &__content {
        font-size: $md-fs;
        outline: none;
        border-radius: 8px;
        background-color: $secondary-fill;
        overflow: hidden;
    }
}

.c-modal-common,
.c-modal-splash {
    @include full-position;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    outline: 0;
    background-color: rgba(125, 200, 255, 0.96);
    font-size: 0;
    white-space: nowrap;
    text-align: center;

    &::before {
        content: '';
        height: 100%;
    }

    &::before,
    &__dialog {
        display: inline-block;
        vertical-align: middle;
    }

    &__dialog {
        text-align: left;
        white-space: initial;
    }

    &__close {
        position: absolute;
    }
}

.c-modal-common {
    padding: $md-gap;

    &__content {
        padding: 56px 104px;
        box-shadow: $box-shadow-xl;
    }

    &__close {
        top: 8px;
        right: 8px;
    }

    &__dialog,
    &--md .c-modal-common__dialog,
    &--md-tiny .c-modal-common__dialog {
        width: 592px;
    }

    &--md-tiny .c-modal-common__content,
    &--2xl-tiny .c-modal-common__content {
        padding-top: 32px;
        padding-bottom: 32px;
    }

    &--lg .c-modal-common__dialog,
    &--lg-wide .c-modal-common__dialog {
        width: 800px;
    }

    &--lg-wide .c-modal-common__content {
        padding-top: 80px;
        padding-bottom: 56px;
    }

    &--xl .c-modal-common__dialog {
        width: 1008px;
    }

    &--2xl .c-modal-common__dialog,
    &--2xl-tiny .c-modal-common__dialog,
    &--2xl-slim .c-modal-common__dialog {
        width: 1216px;
    }

    &--2xl-slim .c-modal-common__content {
        padding-top: 24px;
        padding-bottom: 24px;
    }

    &--full-height {
        .c-modal-common__dialog,
        .c-modal-common__content {
            height: 100%;
        }
    }
}

.c-modal-splash {
    &__content {
        padding: $md-gap;
    }

    &__close,
    &__side-right {
        right: -323px;

        @media screen and (max-width: 1367px) {
            right: -180px;
        }
    }

    &__close {
        top: 24px;
    }

    &__side-right {
        position: absolute;
        top: 88px;
    }

    &::before,
    &__dialog {
        vertical-align: top;
    }

    &__dialog,
    &--md .c-modal-splash__dialog,
    &--md-short .c-modal-splash__dialog {
        width: 599px;
    }

    &--md-short {
        .c-modal-splash__content {
            padding-bottom: 98px;
        }
    }

    &--lg,
    &--lg-narrow {
        .c-modal-splash__dialog {
            width: 711px;
        }
    }

    &--lg-narrow {
        .c-modal-splash__close,
        .c-modal-splash__side-right {
            right: -256px;

            @media screen and (max-width: 1367px) {
                right: -207px;
            }
        }
    }

    &--full-height {
        .c-modal-splash__dialog,
        .c-modal-splash__content {
            height: 100%;
        }
    }

    &--adaptive-center {
        @media screen and (min-width: 1440px) and (min-height: 900px) {
            .c-modal-splash__dialog,
            .c-modal-splash__content {
                height: 100%;
            }

            .c-modal-splash__content {
                display: flex;
                align-items: center;
            }
        }
    }
}

.c-modal-bottom {
    left: 0;
    right: 0;
    bottom: 16px;

    &__dialog {
        width: 1216px;
    }

    &__content {
        padding: 24px 104px;
        box-shadow: $box-shadow-default;
    }
}

