﻿.autocomplete-suggestions {
    background-color: $light-fill;
    border: 1px solid $secondary-border;
    overflow: auto;
    box-shadow: $box-shadow-3xl;
    border-radius: 6px;
    position: absolute;
    left: 0;
    right: 0;
    top: 100%;
    z-index: 2;
}

.autocomplete-suggestion {
    padding: 10px 13px;
    white-space: nowrap;
    overflow: hidden;
    cursor: pointer;
    line-height: $sm-lh;
}

.autocomplete-selected {
    background: $info-steel-fill;
}

.autocomplete-suggestions strong {
    font-weight: $font-weight-bold;
}

.autocomplete-group {
    padding: 10px;
}

.autocomplete-group strong {
    display: block;
}


