.c-signature-field,
.c-signature-draw {
    background: $light-fill;
    border: 1px solid $service-border;
}

.c-signature-field {
    min-height: 477px;
    padding: 24px;

    &__name {
        font-size: $sm-fs;
        line-height: $sm-lh;
        margin-bottom: 20px;
    }
}

.c-signature-draw {
    position: relative;
    min-height: 120px;

    .c-icon-button {
        position: absolute;
        right: 0;
        top: -1px;
    }
}

.c-signature {
    font-size: 0;

    img {
        width: auto;
        max-width: 100%;
    }

    &--left {
        text-align: left;
    }

    &--right {
        text-align: right;
    }

    &--center {
        text-align: center;
    }

    &--medium img {
        max-height: 26px;
    }

    &--large img {
        max-height: 30px;
    }
}

.c-signature-text-placeholder {
    color: #0b0de6;
}


