$max-height: (
    'mh360': 360px,
    'mh377': 377px
);

.c-vertical-scrollbar {
    height: 409px;

    &__inner {
        height: inherit;
        overflow-x: hidden;
        overflow-y: scroll;
        padding-bottom: 10px;
        padding-right: 10px;
        margin-bottom: -10px;
        margin-right: -10px;
        @extend .u-scrollbar-secondary;
    }

    @each $name, $value in $max-height {
        &--#{$name} {
            height: $value;
        }
    }

    &--full-height,
    &--dynamic-height {
        height: 100%;
    }

    &--dynamic-height {
        .c-vertical-scrollbar__inner {
            max-height: calc(100vh - 500px);
            min-height: 325px;
        }
    }
}

