.c-upload-container {
    position: relative;
    background: $light-fill;
    border: 2px dashed $service-border;
    padding: 82px 24px 103px;
    text-align: center;
    height: max-content;

    &.has-error,
    &.has-success {
        border-width: 1px;
        border-style: solid;
    }

    &.has-error {
        .c-upload-container__message {
            display: block;
            color: $danger-text;
            font-weight: $font-weight-bold;
        }
    }

    &.has-success {
        .c-upload-container__toolbar {
            display: flex;

            & + .c-upload-container__image {
                margin-right: 52px;
            }
        }
    }

    .c-upload-container__ico {
        width: 80px;
        height: 80px;
        color: $info-text;
    }

    &__message,
    &__toolbar {
        display: none;
    }

    &__message {
        font-size: $sm-fs;
        margin-top: 9px;

        &:empty {
            margin: 0;
        }
    }

    &__title {
        @extend .c-h3-md;
        margin: 16px 0;

        .c-ico {
            width: 48px;
            height: 84px;
        }
    }

    &__text,
    &__file-name {
        @extend .u-text-bold;
    }

    &__text {
        margin-bottom: 16px;
    }

    &__file-name {
        margin: 32px 0;

        .c-ico {
            margin-right: 8px;
        }
    }

    &__text-help {
        font-size: $xs-fs;
        @extend .u-text-help;
        margin-top: 8px;
    }

    &__image {
        height: 280px;
    }

    &__btn {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 38px;
        max-height: 38px;
        height: 100%;
        padding: 0;
        color: $accent-secondary-text;
        font-size: $_3xs-fs;
        font-weight: $font-weight-bold;
        line-height: $md-lh;
        text-decoration: none;
        white-space: nowrap;
        cursor: pointer;
        background: none;
        outline: none;
        border: 1px solid transparent;
        border-radius: 6px;
        overflow: hidden;

        &:not(:disabled),
        &:not(.is-disabled) {
            &:hover,
            &:focus,
            &:focus-visible {
                text-decoration: none;
                color: $accent-secondary-text;
            }

            &:hover,
            &:focus-visible {
                border-color: $info-border;
            }

            &:active,
            &.is-active {
                background: $light-fill;
                @include  border-outline-color($primary-border);
            }
        }
    }

    &__toolbar {
        display: flex;
        position: absolute;
        right: 0;
        flex-direction: column;
        align-items: center;
        height: 280px;
        padding: 4px 3px;
        background: $light-fill;
        box-shadow: $box-shadow-default;
        border-radius: 6px;

        & > * {
            margin-bottom: 10px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &--compressed {
        padding: 32px 24px;
        width: 280px;
        border-width: 1px;
        box-shadow: $box-shadow-default;

        .c-upload-container__title {
            display: flex;
            align-items: center;
            text-align: left;
            max-width: 179px;
            font-size: $md-fs;
            margin: 0 auto 10px;

            .c-ico {
                width: 48px;
                height: 84px;
                margin-right: 8px;
            }
        }

        &.has-success {
            width: 332px;
            text-align: left;
            padding: 0;
            background: none;
            border: 0;
            box-shadow: none;
        }
    }

    &--border-accent {
        border-color: $accent-border;
    }

    .c-divider {
        margin-bottom: 20px;
    }
}


