$grid-gaps: (
    "none": $grid-gap-none,
    "xxs": $grid-gap-xxs,
    "xs": $grid-gap-xs,
    "sm": $grid-gap-sm,
    "md": $grid-gap-md,
    "xl": $grid-gap-xl,
    "2xl": $grid-gap-2xl,
    "3xl": $grid-gap-3xl,
    "4xl": $grid-gap-4xl,
    "5xl": $grid-gap-5xl,
    "6xl": $grid-gap-6xl,
    "7xl": $grid-gap-7xl
);

$grid-col: (
    "1": 8.33333333%,
    "2": 16.66666667%,
    "3": 25%,
    "4": 33.33333333%,
    "5": 41.66666667%,
    "6": 50%,
    "7": 58.33333333%,
    "8": 66.66666667%,
    "9": 75%,
    "10": 83.33333333%,
    "11": 91.66666667%,
    "12": 100%
);

$grid-col-offset: (
    "0": 0,
    "1": 8.33333333%,
    "2": 16.66666667%,
    "3": 25%,
    "4": 33.33333333%,
    "5": 41.66666667%,
    "6": 50%,
    "7": 58.33333333%,
    "8": 66.66666667%,
    "9": 75%,
    "10": 83.33333333%,
    "11": 91.66666667%
);

// mixins default-gap
@mixin default-gap($gap) {
    margin-right: -$gap;
    margin-left: -$gap;

    & > [class^="grid__col"] {
        padding-right: $gap;
        padding-left: $gap;
    }
}

.grid {
    display: flex;
    flex: 0 1 auto;
    flex-direction: row;
    flex-wrap: wrap;
    @include default-gap($grid-gap-default);

    @each $name, $value in $grid-gaps {
        &--gap-#{$name} {
            @include default-gap($value);
        }
    }

    &--nowrap {
        flex-wrap: nowrap;
    }

    &--reverse {
        flex-direction: row-reverse;
    }

    &--start {
        justify-content: flex-start;
        text-align: start;
    }

    &--center {
        justify-content: center;
    }

    &--end {
        justify-content: flex-end;
        text-align: end;
    }

    &--top {
        align-items: flex-start;
    }

    &--middle {
        align-items: center;
    }

    &--bottom {
        align-items: flex-end;
    }

    &--around {
        justify-content: space-around;
    }

    &--between {
        justify-content: space-between;
    }

    &--column {
        flex-direction: column;
        height: 100%;
    }

    &--canvas {
        flex: 1;

        & > [class^="grid__col"] {
            display: flex;
            flex-direction: column;
        }
    }

    &__col-common {
        flex: 1;
    }

    @each $name, $value in $grid-col {
        &__col-#{$name} {
            flex-basis: $value;
            max-width: $value;
            width: $value;
        }
    }

    @each $name, $value in $grid-col-offset {
        &__col-offset-#{$name} {
            margin-left: $value;
        }
    }
}


