.c-container-flex {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    overflow: hidden;

    &__top {
        padding-bottom: 24px;
    }

    &__content {
        flex: 1;
        display: flex;
        flex-wrap: nowrap;
        height: 100%;
        overflow: hidden;
    }

    &__cell,
    &__cell-common {
        height: 100%;
        overflow-x: hidden;
        overflow-y: scroll;
        @extend .u-scrollbar-secondary;
        margin-right: 32px;

        &:last-child {
            margin-right: 0;
        }
    }

    &__cell-common {
        flex: 1;
    }

    &__bottom {
        padding-top: 24px;
    }
}


