.c-resume-template,
.c-resume-template-section {
    @include user-select-none;
}

.c-resume-template {
    min-height: 795px;
    border-radius: 8px;
    box-shadow: 0 3px 7px 0 rgba(0, 0, 0, .1);

    .c-resume-template-title__btn {
        @extend .c-link;
        @extend .u-text-xs;
        font-family: $font-family;
        font-weight: $font-weight-bold;

        .c-ico {
            margin: -3px 4px 0 0;
        }
    }

    .c-resume-template-section.is-highlighted {
        .c-resume-template-section__inner {
            position: relative;
            z-index: 1;

            &::before {
                position: absolute;
                top: -16px;
                bottom: -16px;
                left: -16px;
                right: -16px;
                content: '';
                border: 8px solid $info-border;
                border-radius: 12px;
                box-shadow: 0 8px 14px 0 rgba(0, 0, 0, .25);
            }
        }
    }

    &.page-size-letter {
        width: 704px;
    }

    &.page-size-a4 {
        width: 684px;
    }

    .c-edit-panel {
        margin-bottom: 0;
    }

    &.bright,
    &.modern,
    &.techno {
        .c-resume-template-head > .c-edit-panel:first-child {
            width: 100%;
        }
    }

    &.bright,
    &.modern {
        .c-resume-template-head > .c-edit-panel:first-child {
            position: absolute;

            .c-resume-template-photo {
                position: static;
            }
        }
    }

    &.bright .c-resume-template-head {
        & > .c-edit-panel:first-child {
            top: -32px;
            left: 15px;
            max-width: 115px;
            height: 115px;

            & + .c-resume-template-data {
                & > * > * > .c-resume-template-headline,
                & > .c-resume-template-headline {
                    .c-resume-template-headline__name::before {
                        display: block;
                    }
                }

                & > * > * > .c-resume-template-contacts,
                & > .c-resume-template-contacts {
                    .c-resume-template-title::before,
                    .c-resume-template-title__text,
                    .c-resume-template-title__btn {
                        display: none;
                    }
                }
            }
        }
    }

    &.modern .c-resume-template-head {
        & > .c-edit-panel:first-child {
            top: 40px;
            left: 59.5px;
            max-width: 96px;
            height: 96px;
            z-index: 2;

            & + .c-resume-template-data {
                padding: 40px 24px 32px 239px;

                &::before {
                    display: block;
                }
            }
        }
    }

    &.techno .c-resume-template-head {
        & > .c-edit-panel:first-child {
            max-width: 56px;
            height: 56px;
            margin: auto;
        }
    }

    &.innovative .c-resume-template-data::before,
    &.heart .c-resume-template-head::before {
        border-top-left-radius: 8px;
    }

    &.innovative .c-resume-template-summary::before,
    &.heart .c-resume-template-head::before {
        border-top-right-radius: 8px;
    }

    &.innovative {
        .c-resume-template-side::before {
            border-bottom-left-radius: 8px;
        }

        .c-resume-template-main::before {
            border-bottom-right-radius: 8px;
        }

        .c-resume-template-data + .c-template-dnd-section {
            flex: 2;
            min-height: 100%;
            display: flex;
            flex-direction: column;
        }

        .c-edit-panel {
            &:last-child {
                flex: 2;
                height: 100%;

                .c-edit-panel__content,
                .c-resume-template-summary {
                    height: 100%;
                }
            }

            &:hover {
                .c-edit-panel__content .c-resume-template-summary::before {
                    background: #ffffff;
                    opacity: 0;
                }
            }
        }
    }

    &.creative,
    &.charming {
        .c-edit-panel:hover {
            .c-edit-panel__content .c-resume-template-contacts .c-resume-template-section__inner {
                color: #ffffff;
            }
        }
    }

    &.executive,
    &.modern,
    &.retro,
    &.nexis,
    &.striking .c-resume-template-side {
        .c-template-dnd-section.is-dragging,
        .c-edit-panel:hover {
            .c-edit-panel__content {
                .c-resume-template-section__inner,
                .c-resume-template-contacts .c-resume-template-contacts__icon-set,
                .c-resume-template-headline__name > * {
                    color: #000000;
                }
            }
        }
    }

    &.striking {
        .c-resume-template-side .c-resume-template-language:hover .c-resume-template-language__line {
            border-color: #000000;

            &--fill {
                background: #000000;
            }
        }
    }

    &.fashionable .c-resume-template-head {
        .c-edit-panel + .c-resume-template-data {
            margin-left: 134px;
        }
    }

    &.luxurious {
        .c-edit-panel:hover .c-resume-template-headline__name {
            &::before {
               display: none;
            }
        }
    }
}


