.c-quote-container {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding: 23px 23px 38px;
    border: 1px solid $primary-border;
    border-radius: 8px;
    background: $light-fill;

    &__ico {
        width: 64px;
        height: 64px;
    }

    &__text {
        flex: 1;
        font-size: $lg-fs;
        margin: 10px 0 30px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__img {
        text-align: right;
        margin-bottom: -15px;
    }

    &--expanded,
    &--compact {
        padding: 0;
        border: none;
        border-radius: 0;
    }

    &--compact {
        flex-direction: row;
        padding: 16px;

        .c-quote-container__ico {
            width: 24px;
            height: 24px;
            margin-right: 8px;
        }

        .c-quote-container__text {
            font-size: $md-fs;
            margin: 0;
        }
    }

    &--expanded {
        .c-quote-container__ico {
            width: 48px;
            height: 48px;
        }

        .c-quote-container__text {
            line-height: 1.7;
            margin-top: 8px;
        }
    }
}


