.c-counts-progress {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    padding: 8px 15px;
    background: $secondary-fill;
    border-radius: 6px;

    &__content {
        flex: 1;
        margin-right: 12px;

        &:last-child {
            margin-right: 0;
        }
    }

    &__top {
        margin-bottom: 6px;
    }

    &__title {
        @extend .c-h5;
    }

    &__text-info {
        @extend .u-text-help;
        @extend .u-text-normal;
        margin-left: 8px;
    }

    &__scale,
    &__line {
        height: 5px;
        border-radius: 6px;
        background: $secondary-dark-fill;
    }

    &__scale {
        width: 100%;
    }

    &__line {
        width: 0;
        transition: width 0.4s ease-in;
    }

    $counts-backgrounds: (
        1 3: $danger-dark-fill,
        4 5: $warning-fill,
        6 12: $success-fill
    );

    @each $range, $color in $counts-backgrounds {
        @for $i from nth($range, 1) through nth($range, 2) {
            &--counts-#{$i} {
                .c-counts-progress__line {
                    background: $color;
                }
            }
        }
    }

    &--counts-limit {
        .c-counts-progress__line {
            background: $accent-secondary-fill;
        }
    }

    @for $i from 1 through 6 {
        &--counts-#{$i} {
            .c-counts-progress__line {
                width: #{($i * 16.66667%)};
            }
        }
    }

    &--counts-7,
    &--counts-8,
    &--counts-9,
    &--counts-10,
    &--counts-11,
    &--counts-12,
    &--counts-limit {
        .c-counts-progress__line {
            width: 100%;
        }
    }
}


