.c-plans {
    position: relative;
    display: flex;
    align-items: center;

    &__box {
        display: inline-block;
        width: 100%;
        max-width: 384px;
        min-height: 385px;
        padding: 32px;
        background: $secondary-light-fill;
        border-radius: 8px;
        border: 1px solid $secondary-border;
        z-index: 0;

        &:first-child {
            border-right: 0;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
        }

        &:last-child {
            border-left: 0;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;

            .c-plans__select {
                left: auto;
                right: 0;
            }
        }

        &--highlight {
            min-height: 495px;
            max-width: 448px;
            padding: 48px 64px;
            background: $light-fill;
            border: 0;
            box-shadow: $box-shadow-lg;
            z-index: 1;

            .c-plans__title {
                font-size: 24px;
                font-weight: $font-weight-bold;
                margin-bottom: 28px;
            }
        }
    }

    &__head {
        position: relative;
        padding: 0 32px 24px;
        border-bottom: 1px solid $secondary-border;
    }

    &__content {
        padding-top: 32px;
    }

    &__select {
        position: absolute;
        top: 0;
        left: 0;
    }

    &__title {
        font-size: $md-fs;
        text-align: center;
        margin-bottom: 18px;
    }

    &__value {
        text-align: center;
        font-size: $_2xl-fs;
        font-weight: $font-weight-bold;
        line-height: $xs-lh;
    }

    &__value-sub,
    &__value-symbol {
        position: relative;
        line-height: 0;
        vertical-align: baseline;
        margin-left: 3px;
    }

    &__value-sub {
        top: -14px;
        font-size: $lg-fs;
    }

    &__value-symbol {
        top: -11px;
        font-size: 24px;
    }
}


