.c-list-icon {
    margin: 0;
    padding: 5px 0;
    list-style: none;
    border-radius: 6px;
    background: $light-fill;
    box-shadow: $box-shadow-default;

    &__item {
        display: block;

        &.is-active {
            .c-list-icon__link {
                background: $info-steel-fill;
                border-color: $primary-border;

                &::after {
                    position: absolute;
                    display: inline-block;
                    content: '';
                    @include center-vertical-position;
                    right: 14px;
                    @include bg-svg($check);
                }
            }
        }
    }

    &__link {
        position: relative;
        display: block;
        color: $primary-fill;
        line-height: $md-lh;
        padding: 10px 48px 10px 16px;
        border: 2px solid transparent;
        outline: none;
        background: none;
        transition: .12s;

        @include hover-focus {
            color: $primary-text;
        };

        &:hover,
        &:focus,
        &:active {
            background: $info-steel-fill;
        }

        &:active {
            border-color: $primary-border;
        }
    }

    &__selected {
         font-weight: $font-weight-bold;
        background: $danger-light-fill;
    }

    &--scroll {
        height: 377px;
        overflow-x: hidden;
        overflow-y: auto;
        @extend .u-scrollbar-secondary;
    }
}


