.c-nav-tabs {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid $secondary-border;

    &__title,
    &__link {
        color: $primary-text;
        font-size: $md-fs;
        line-height: $md-lh;
    }

    &__title {
        font-weight: $font-weight-bold;
        margin-right: 56px;
    }

    &__list {
        display: flex;
        @include reset-list;
    }

    &__item,
    &__link {
        position: relative;
        display: inline-flex;
    }

    &__item {
        margin: 0 28px;

        &:first-child {
            margin-left: 0;
        }

        &:last-child {
            margin-right: 0;
        }
    }

    &__link {
        flex-direction: row;
        align-items: center;
        justify-content: center;
        text-align: center;
        max-width: 100%;
        padding: 18px 0;
        border: none;
        background: none;
        outline: none;

        &::after {
            display: none;
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            bottom: -1px;
            height: 3px;
            background: $primary-fill;
        }

        &:hover,
        &:focus-visible,
        &:active,
        &.active, {
            &::after {
                display: block;
            }
        }

        &:active,
        &.active {
            font-weight: bold;
        }

        &:hover:not(.active) {
            &::after {
                background: $info-fill;
            }
        }

        .c-ico {
            margin: -2px 10px 0 -2px;
        }
    }

    &__tooltip {
        position: absolute;
        right: 0;
        @include center-vertical-position;
        z-index: 1;

        &:hover {
            .c-tooltip {
                display: block;
            }
        }

        & + .c-nav-tabs__link {
            padding-right: 23px;
        }

        .c-tooltip {
            display: none;
            position: absolute;
            top: -23px;
            left: 100%;
        }
    }

    &--content-between {
        justify-content: space-between;
    }
}

.c-tab-content {
    display: none;

    &.active {
        display: block;
    }
}


