$size-values: (104, 176);

// Mixin size image
@mixin image-size($size) {
    width: $size;
    height: $size;
}

.c-image {
    display: inline-block;
    vertical-align: top;

    @each $i in $size-values {
        &--wh#{$i} {
            @include image-size(#{$i}px);
        }
    }

    &--full {
        display: block;
        width: 100%;
    }

    &--round {
        border-radius: 6px;
    }
}


