.c-content-tooltip {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;

    &__title {
       font-size: $md-fs;
    }

    &__text {
        position: relative;
        font-size: $xs-fs;
        color: $info-text;
        text-decoration: underline;

        &:hover {
            .c-tooltip {
                display: block;
            }
        }

        .c-tooltip {
            display: none;
            position: absolute;
            top: 100%;
            @include center-horizontal-position;
        }
    }

    &__container {
        &:not(:only-child) {
            margin-left: 8px;
        }
    }
}
