.c-count-down {
    display: inline-flex;
    justify-content: flex-end;
    position: relative;
    background-color: $secondary-fill;
    width: 198px;
    height: 46px;
    padding: 5px 13px;
    border: 1px solid $service-border;
    border-radius: 10px;
    z-index: 1;

    &__timer {
        position: relative;
        display: inline-flex;
        justify-content: flex-start;
        align-items: center;
        height: 100%;
        padding: 0 6px 0 12px;
        color: $danger-text;
        font-size: 26px;
        font-weight: $font-weight-bold;
        white-space: nowrap;

        &::before,
        &::after {
            position: absolute;
            content: '';
            width: 6px;
            height: 6px;
            background: $accent-secondary-fill;
            opacity: 0.20;
            border-radius: 50%;
            @include center-vertical-position;
        }

        &::before {
            left: 0;
        }

        &::after {
            right: 0;
        }

        span {
            display: inline-block;
            width: 42px;
            letter-spacing: 5px;
            text-align: center;

            &:nth-child(even) {
                width: auto;
            }
        }
    }

    &__img {
        position: absolute;
        top: -12px;
        left: -62px;
        z-index: 2;
    }
}


