.c-template-dnd-section.is-dragging,
.c-edit-panel:hover {
    .c-edit-panel__wrap {
        display: block;
    }

    .c-edit-panel__content {
        position: relative;
        z-index: 3;
    }

    .c-resume-template-title__btn {
        display: inline-block;
    }
}

.c-edit-panel {
    position: relative;
    cursor: pointer;
    margin-bottom: 24px;

    &:last-child {
        margin-bottom: 0;
    }

    &__title {
        color: $primary-text;
        font-size: $md-fs;
        font-family: $font-family;
        font-weight: $font-weight-bold;
        width: max-content;
        margin-right: 10px;
        word-break: break-all;
    }

    .c-edit-panel__btn,
    &__tools,
    &__wrap {
        position: absolute;
    }

    &__tools,
    &__wrap {
        box-shadow: $box-shadow-xl;
    }

    .c-edit-panel__btn,
    &__tools {
        z-index: 4;
    }

    &__wrap {
        display: none;
        top: -24px;
        bottom: -24px;
        left: -83px;
        right: -83px;
        background: $light-fill;
        border: 2px solid $info-border;
        border-radius: 8px;
        z-index: 3;
    }

    &__tools {
        display: flex;
        align-items: center;
        flex-wrap: nowrap;
        max-width: calc(100% - 10px);
        top: 100%;
        @include center-horizontal-position;
        min-height: 50px;
        margin-top: 2px;
        padding: 5px 20px;
        background: $light-fill;
        border-radius: 0 0 6px 6px;

        .c-icon-button {
            margin-right: 6px;

            &:first-child {
                margin-left: -12px;
            }

            &:last-child {
                margin-right: -12px;
            }
        }
    }

    .c-edit-panel__btn {
        right: 12px;
        @include center-vertical-position;

        &:active {
            transform: translateY(-50%) scale(.8);
        }
    }

    &__content {
        @include user-select-none;
    }

    &--sm {
        .c-edit-panel__wrap {
            left: -24px;
            right: -62px;
        }

        .c-edit-panel__title {
            font-size: $sm-fs;
        }
    }

    .c-resume-template-title__btn {
        display: none;
    }
}


