.form-group {
    position: relative;

    &__container,
    &__field {
        position: relative;
    }

    &__field {
        .form-group__field-btn + .form-control {
            padding-right: 56px;
        }
    }

    &__field-btn,
    &__field-autocomplete {
        position: absolute;
    }

    &__field-btn {
        @include center-vertical-position;
        right: 8px;
        height: 100%;
        color: $accent-secondary-text;

        &.c-icon-button {
            &:active {
                transform: translateY(-50%) scale(.8);
            }
        }
    }

    &__field-autocomplete {
        @extend .autocomplete-suggestions;
        overflow-x: hidden;
        overflow-y: auto;
        max-height: 367px;
        @extend .u-scrollbar-secondary;
    }

    &__field-section {
        padding: 16px;

        &:not(:last-child) {
            border-bottom: 1px solid $secondary-border;
        }
    }

    &__placeholder {
        font-size: $md-fs;
        color: $accent-secondary-text;
        pointer-events: none;
        @extend .u-text-single-line;
    }

    &.has-success,
    &.has-error,
    &__container,
    &--secure {
        input.form-control {
            padding-right: 48px;
        }

        .help-block {
            display: block;
        }
    }

    &.has-success {
        input.form-control {
            background: url('%=static=%general/check-info.svg') no-repeat right 16px center $light-fill;
        }
    }

    &.has-error {
        .form-control,
        .form-group__container {
            border-width: 2px;
            border-color: $danger-border;
        }

        input.form-control {
            background: url('%=static=%general/alert-octagon-danger.svg') no-repeat right 16px center $light-fill;
        }

        .help-block {
            color: $danger-text;
            font-weight: $font-weight-bold;
        }
    }

    &.form-group--secure {
        input.form-control {
            background: url('%=static=%general/lock-secondary.svg') no-repeat right 16px center;
        }
    }

    &--compressed {
        .control-label {
            @extend .w-gap-xs;
        }
    }

    &--label-sm {
        .control-label {
            @extend .c-h5;
        }
    }

    &--base-width {
        width: 100%;
        max-width: 224px;
    }

    &--xs {
        .form-control {
            @include input-size(40px);
        }
    }

    &--selection .form-group__placeholder,
    &--dynamic-label .control-label {
        position: absolute;
        top: 16px;
        left: 16px;
        transition: all .08s;
    }

    &--dynamic-label {
        .control-label {
            font-size: $md-fs;
            font-weight: $font-weight-regular;
            color: $accent-secondary-text;
            margin: 0;
            z-index: 1;
        }

        .form-control,
        .form-control::placeholder {
            color: transparent;
        }

        textarea.form-control,
        &.is-focus textarea.form-control, {
            margin-top: 30px;
            margin-bottom: 16px;
            padding-top: 0;
            padding-bottom: 0;
            line-height: $md-lh;
        }

        &.is-focus {
            .control-label,
            .form-control {
                color: $primary-text;
            }

            .control-label {
                top: 8px;
                font-size: $xs-fs;
                font-weight: $font-weight-bold;
            }

            .form-control {
                font-size: $md-fs;
                line-height: $xs-lh;
                padding-top: 20px;
            }
        }

        .form-group__container {
            display: block;
            width: 100%;
            outline: none;
            background: $light-fill;
            border: 1px solid $service-border;

            &:hover {
                border: 1px solid $info-border;
            }

            &:active,
            &:focus {
                border: 1px solid $primary-border;
            }

            select.form-control,
            textarea.form-control {
                border: none;

                &:hover,
                &:active,
                &:focus,
                &.is-disabled,
                &:disabled {
                    border: none;
                }

                &.is-disabled,
                &:disabled {
                    background-color: inherit;
                }
            }

            textarea.form-control {
                background: inherit;
            }
        }
    }

    &--selection {
        .form-group__placeholder {
            right: 32px;
        }

        .form-control {
            color: transparent;
        }

        &.is-selected {
            .form-group__placeholder {
                display: none;
            }

            .form-control {
                color: $primary-text;
            }
        }
    }

    &--toggle,
    &--toggle-checkbox-view {
        label:not(.control-label) {
            display: block;
            margin-bottom: 24px;

            &:last-of-type {
                margin-bottom: 0;
            }

            &:hover,
            &:focus {
                input[type="checkbox"]:not(:disabled) + span::before,
                input[type="radio"]:not(:disabled) + span::before {
                    border-color: $info-border;
                }
            }

            &:active {
                input[type="checkbox"]:not(:disabled) + span::before,
                input[type="radio"]:not(:disabled) + span::before{
                    border-color: $primary-border;
                }
            }

            input {
                @include invisible-input;

                &[type="checkbox"],
                &[type="radio"] {
                    &:disabled + span {
                        cursor: none;
                        pointer-events: none;
                        opacity: .5;
                    }

                    &:checked + span::after {
                        opacity: 1;
                    }
                }

                &[type="checkbox"] + span::after {
                    left: 0;
                    top: 0;
                    @include bg-svg($check);
                }

                &[type="radio"]  + span {
                    &::before {
                        border-radius: 50%;
                    }

                    &::after {
                        left: 6px;
                        top: 6px;
                        @include bg-svg($ellipse);
                    }
                }
            }

            span {
                position: relative;
                display: inline-block;
                min-width: 24px;
                min-height: 24px;
                padding-left: 40px;
                cursor: pointer;

                &::before,
                &::after {
                    content: '';
                    display: inline-block;
                    position: absolute;
                }

                &::before {
                    top: 0;
                    left: 0;
                    width: 24px;
                    height: 24px;
                    border: 1px solid $service-border;
                    background: $light-fill;
                }

                &::after {
                    opacity: 0;
                }

                &:empty {
                    padding-left: 0;
                }
            }
        }

        &.form-group--compacted {
            label:not(.control-label) {
                margin-bottom: 16px;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }

    &--toggle-checkbox-view {
        label:not(.control-label) {
            input[type="radio"] + span {
                &::after {
                    left: 0;
                    top: 0;
                    @include bg-svg($check);
                }

                &::before {
                    border-radius: 0;
                }
            }
        }
    }

    .c-tooltip {
        display: none;
        position: absolute;
        top: 100%;
    }
}

.help-block {
    display: none;
    font-size: $sm-fs;
    text-align: left;
    margin-top: 9px;

    &:empty {
        margin: 0;
    }
}


