.c-divider {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $accent-secondary-text;
    font-size: $md-fs;
    height: 100%;
    text-transform: uppercase;
    white-space: nowrap;

    &::before,
    &::after {
        content: '';
        width: 2px;
        background-color: $secondary-dark-fill;
        height: 100%;
    }

    &::before {
        margin: 0 0 5px 0;
    }

    &::after {
        margin: 5px 0 0 0;
    }

    &--horizontal {
        flex-direction: row;
        height: auto;

        &::before,
        &::after {
            content: '';
            width: 100%;
            height: 2px;
        }

        &::before {
            margin: 0 5px 0 0;
        }

        &::after {
            margin: 0 0 0 5px;
        }
    }
}


