.c-nav-menu {
    margin: 0;
    padding: 0;
    background: $secondary-fill;
    border-radius: 8px;
    overflow: hidden;

    &__item {}

    &__link {
        display: block;
        color: $primary-text;
        font-size: $sm-fs;
        padding: 13px 16px;

        @include hover-focus;

        &:hover,
        &:focus-visible {
            background: $info-steel-fill;
        }

        &.active {
            font-weight: $font-weight-bold;
        }
    }
}
