.c-preview-list,
.c-preview-card {
    &__name {
        .c-link {
            @extend .c-link--primary;
            font-weight: $font-weight-bold;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            line-height: $md-lh;
        }
    }
}

.c-preview-list {
    @media (max-width: 1260px) {
        .c-preview-list__actions {
            padding-left: 32px;
        }

        .c-preview-list__head {
            .c-preview-list__actions {
                padding-left: 48px;
            }
        }
    }

    &__body,
    &__head {
        padding: 16px 24px;
    }

    &__body {
        background: $light-fill;
        border-radius: 6px;
    }

    &__head {
        display: flex;
        padding-top: 0;

        & > * {
            color: $accent-secondary-text;
            font-size: $sm-fs;
            font-weight: $font-weight-bold;
            padding: 0 16px;

            &:first-child {
                padding-left: 0;
            }

            &:last-child {
                padding-right: 0;
            }
        }

        .c-preview-list__date,
        .c-preview-list__progress {
            padding-left: 0;
        }

        .c-preview-list__actions {
            padding-left: 131px;
        }
    }

    &__table {
        width: 100%;
        border-collapse: collapse;

        tr {
            &:first-child td {
                padding-top: 0;
            }

            &:last-child td {
                border-bottom: 0;
                padding-bottom: 0;
            }

            td {
                border-bottom: 1px solid $secondary-border;
                padding-top: 16px;
                padding-bottom: 16px;
                padding-right: 16px;

                &:last-child {
                    padding-right: 0;
                }
            }
        }
    }

    &__name {
        width: 33.3%;

        .c-link {
            max-width: 362px;
            display: block;
        }
    }

    &__message {
        font-size: 12px;
        color: $accent-secondary-text;
    }

    &__date {
        width: 12.7%;

        &:nth-last-child(2) {
            width: 29.5%;
        }

        .c-preview-list__item {
            width: 124px;

            &:first-child {
                padding-right: 12px;
            }

            &:last-child:not(:only-child) {
                padding-left: 12px;
            }
        }
    }

    &__creation,
    &__progress {
        width: 16.8%;
    }

    &__progress {
        text-align: center;

        .c-badge {
            margin: 0 auto;
        }
    }

    &__actions {
        width: 37.2%;
        padding-left: 115px;
    }
}

.c-preview-card {
    position: relative;
    padding: 23px;
    min-height: 568px;
    background: $info-steel-fill;
    border: 1px solid $service-border;
    border-radius: 6px;
    box-shadow: $box-shadow-default;
    cursor: pointer;
    z-index: 1;

    &:hover,
    &:focus {
        border: 1px solid $info-border;

        .c-preview-card__name > * {
            color: $info-text;
            text-decoration: underline;
        }
    }

    &__img {
        display: flex;
        justify-content: center;

        img {
            display: block;
            width: 240px;
            height: auto;
            border-radius: 6px;
            box-shadow: $box-shadow-default;
        }
    }

    &__panel {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 23px;
        background: $light-fill;
        border-top: 1px solid $secondary-border;
        border-radius: 0 0 6px 6px;
        z-index: 2;
    }

    &__name {
        margin-bottom: 16px;

        & > * {
            display: block;
            width: 100%;
        }
    }

    &__info,
    &__actions {
        display: flex;
        flex-direction: row;
    }

    &__info {
        justify-content: space-between;
        font-size: $sm-fs;
        margin-bottom: 22px;
    }

    &__actions {
        justify-content: space-between;

        & > * {
            margin-right: 16px;

            &:last-child {
                margin-right: 0;
            }
        }
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: center;

        .c-badge {
            margin-left: 6px;
        }
    }
}


