.c-counter {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 154px;
    width: 100%;
    height: 58px;
    padding: 0 20px 0 55px;

    &::before {
        content: '';
        position: absolute;
        @include full-position;
        background: {
            image: url("%=static=%general/counter-bg-2.svg");
            repeat: no-repeat;
        }
    }

    &::after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 33px;
        right: 0;
        background: {
            image: url("%=static=%general/counter-bg-1.svg");
            repeat: no-repeat;
            size: 100% 58px;
        }
    }

    &__value {
        position: relative;
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
        color: $light-text;
        font-weight: $font-weight-bold;
        font-size: $xl-fs;
        z-index: 1;
        transition: 0.3s linear;
        letter-spacing: 5px;
    }

}


