a,
.c-link {
    color: $info-text;
    text-align: left;
    text-decoration: none;
    cursor: pointer;
    background: none;
    border: none;
    outline: none;
    padding: 0;

    &:hover,
    &:focus {
        text-decoration: underline;
    }
}

button.c-link {
    &:disabled,
    &.is-disabled {
        color: $secondary-dark-text;
        pointer-events: none;

        &:hover,
        &:focus {
            text-decoration: none;
            color: $secondary-dark-text;
        }
    }
}

.c-link {
    &--primary,
    &--accent {
        &:hover,
        &:focus {
            color: $info-text;
        }
    }

    &--primary {
        color: $primary-text;
    }

    &--accent {
        color: $accent-secondary-text;
    }

    &--underline {
        text-decoration: underline;

        &:hover,
        &:focus {
            text-decoration: none;
        }
    }

    &--hover-danger {
        &:hover,
        &:focus {
            color: $danger-text;
        }
    }
}


