.c-stars {
    display: flex;
    align-items: center;

    span {
        position: relative;
        display: inline-block;
        width: 24px;
        height: 24px;

        &::before {
            position: absolute;
            display: inline-block;
            content: '';
            @include center-position;
            @include bg-svg($star-lg);
        }
    }
}


