.c-logo {
    display: inline-block;
    vertical-align: middle;
    font-size: 0;
    width: 240px;
    height: 18px;

    &::before {
        content: '';
        display: block;
        @include bg-svg($logo);
    }

    &:hover,
    &:focus-visible {
        &::before {
            @include bg-svg($logo-info);
        }
    }

    &:active  {
        &::before {
            @include bg-svg($logo-dark);
        }
    }

    &--resume {
        &::before {
            @include bg-svg($resume-logo);
        }

        &:hover,
        &:focus-visible {
            &::before {
                @include bg-svg($resume-logo-info);
            }
        }

        &:active  {
            &::before {
                @include bg-svg($resume-logo-dark);
            }
        }
    }
}


