.c-review,
.c-review-card {
    max-width: 384px;
    width: 100%;
    border-radius: 8px;
}

.c-review {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 24px;
    background: $secondary-fill;

    &__tag,
    &__data {
        color: $accent-secondary-text;
    }

    &__data {
        font-size: $xs-fs;
        line-height: $xs-lh;
    }

    &__tag,
    &__text {
        font-size: $sm-fs;
    }

    &__tag {
        .c-ico {
            margin: -2px 6px 0 0;
        }
    }

    &__text {
        flex: 1;
        margin-bottom: 20px;
        @extend .u-text-3-line;

        &:last-child {
            margin-bottom: 0;
        }
    }

    .c-h5 {
        margin: 0 0 7px 0;
    }
}

.c-review-card {
    padding: 24px 32px 32px;
    color: $light-text;
    background: $info-light-fill;

    &__avatar {
        overflow: hidden;
        border-radius: 45px;
        max-width: 92px;
        width: 100%;
    }

    &__text {
        font-size: $lg-fs;
    }

    .c-stars {
        margin-bottom: 24px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &--lg {
        padding: 48px 104px;
    }

    &--full {
        max-width: 100%;
    }

    &--image-wide {
        .c-review-card__avatar {
            max-width: 198px;
        }
    }
}


