// TYPOGRAPHY

// FONT-FAMILY
$font-family: 'Manrope', sans-serif;


//FONT-WEIGHT
$font-weight-regular: 400;
$font-weight-bold: 700;


// FONT-SIZE

$_3xs-fs: 8px;
$_2xs-fs: 10px;
