// GAPS

// WRAPPER-GAPS
$xs-gap: 8px;
$sm-gap: $xs-gap * 2;
$md-gap: $xs-gap * 3;
$lg-gap: $xs-gap * 4;
$xl-gap: $xs-gap * 5;
$_2xl-gap: $xs-gap * 6;
