$_5xl-gap: $xs-gap * 9;
$_6xl-gap: $xs-gap * 10;
$_7xl-gap: $xs-gap * 11;
$_8xl-gap: $xs-gap * 12;
$extra-gap: 120px;


// GRID-GAPS
$grid-gap-xxs: 2px;
$grid-gap-xs: 4px;
$grid-gap-sm: 8px;
$grid-gap-md: 12px;
$grid-gap-lg: 16px;
$grid-gap-xl: 20px;
$grid-gap-2xl: 24px;
$grid-gap-3xl: 28px;
$grid-gap-4xl: 32px;
$grid-gap-5xl: 36px;
$grid-gap-6xl: 40px;
$grid-gap-7xl: 44px;
$grid-gap-default: $grid-gap-lg;
$grid-gap-none: 0;


// ROW-GAPS
$row-gap-xxs: 4px;
$row-gap-xs: 8px;
