.c-card-box {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 32px 96px 32px 32px;
    color: $primary-text;
    background: $light-fill;
    border: 1px solid transparent;
    border-radius: 8px;
    box-shadow: $box-shadow-default;
    cursor: pointer;

    &:hover,
    &:focus {
        border-color: $info-border;
        text-decoration: none;
    };

    &__ico {
        position: absolute;
        top: 32px;
        right: 32px;
    }

    &__image {
        margin: 0 0 19px 0;
    }

    &__title {
        @extend .c-h3;
        margin-bottom: 16px;
    }

    &__description,
    &__subtitle {
        color: $accent-secondary-text;
    }

    &__subtitle {
        @extend .u-text-single-line;
    }

    &--success {
        color: $light-text;
        background: $success-secondary-fill;

        .c-card-box__description {
            color: inherit;
        }
    }


    &--icon-position,
    &--row {
        .c-card-box__ico {
            right: 47px;
        }
    }

    &--row {
        .c-card-box__ico {
            @include center-vertical-position;
        }
    }

    &--icon-position {
        .c-card-box__ico {
            top: 29px;
            transform: translateY(0);
        }
    }

    &--row,
    &--expanded {
        flex-direction: row;
        flex-wrap: nowrap;

        .c-card-box__title {
            margin-bottom: 8px;
        }

        .c-card-box__image {
            margin: 0 33px 0 0;
        }

        .c-card-box__content {
            width: 339px;
        }
    }

    &--expanded {
        align-items: center;
        padding: 20px 96px 20px 32px;

        .c-card-box__title {
            @extend .c-h3-md;
        }

        .c-card-box__description {
            @extend .u-text-lg
        }
    }

    &--outline {
        outline: 3px solid $success-border;

        &:hover,
        &:focus {
            outline-color: transparent;
        }
    }

    &--content-auto {
        .c-card-box__content {
            width: auto;
        }
    }
}


