.c-vertical-option-btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    height: 88px;
    color: $primary-text;
    font-size: $xs-fs;
    font-weight: $font-weight-bold;
    line-height: $xs-lh;
    padding: 15px;
    background: transparent;
    border-radius: 6px;
    border: 1px solid transparent;
    outline: 1px solid transparent;
    outline-offset: -2px;

    &:not(:disabled),
    &:not(.is-disabled) {
        &:hover,
        &:focus,
        &:focus-visible {
            text-decoration: none;
        }

        &:hover,
        &:focus-visible {
            border-color: $info-border;
        }

        &:hover,
        &:focus {
            color: $primary-text;
        }

        &:active,
        &.is-active,
        &.show {
            background: $light-fill;
            @include  border-outline-color($primary-border);
        }
    }

    &:disabled,
    &.is-disabled {
        opacity: 0.7;
        pointer-events: none;
    }

    .c-ico {
        margin-bottom: 14px;
    }

    &--sm {
        height: 62px;
        padding: 8px 15px;

        .c-ico {
            margin-bottom: 8px;
        }
    }

    &--xs {
        height: 54px;
        padding: 6px 8px;

        .c-ico {
            margin-bottom: 6px;
        }
    }

    &--secondary {
        color: $accent-secondary-text;

        &:not(:disabled),
        &:not(.is-disabled) {
            &:hover,
            &:focus {
                color: $accent-secondary-text;
            }
        }
    }

    &--row {
        flex-direction: row;
        justify-content: center;
        text-align: left;
        width: 100%;
        height: auto;
        padding: 15px 15px 14px;
        line-height: $sm-lh;

        .c-ico {
            margin-right: 8px;
            margin-bottom: 0;
        }

        &.c-vertical-option-btn--sm {
            height: auto;
            padding: 7px;
        }
    }
}


