.c-header {
    position: relative;
    border-bottom: 1px solid $secondary-border;
    background-color: $light-fill;

    &__top,
    &__cell {
        display: flex;
        flex-wrap: nowrap;
    }

    &__top {
        align-items: center;
        justify-content: space-between;
        padding: 12px 0;
        min-height: 64px;

        .c-header__cell:last-child {
            .c-btn {
                min-height: 40px;
                font-size: $sm-fs;
                white-space: nowrap;
            }

            & > * {
                margin-left: 24px;

                &:first-child {
                    margin-left: 0;
                }
            }
            .c-navbar {
                margin-right: -8px;
            }
        }
    }

    .c-dropdown__menu {
        margin-top: 13px;
    }

    &--secondary {
        background-color: $secondary-fill;
    }
}


