.c-star-rating {
    display: flex;
    align-items: center;

    span {
        position: relative;
        width: 20px;
        height: 20px;
        border-width: 0 9.9px;
        border-style: solid;
        border-color: #DCDCE6;
        border-radius: 50%;
        background: #DCDCE6;
        margin-right: 4px;

        &::before {
            content: '';
            @include bg-svg($star);
            position: absolute;
            top: 50%;
            left: 50%;
            margin-top: -7px;
            transform: translateX(-50%);
        }

        &:last-child {
            margin-right: 0;
        }
    }

    @function stars-color($num) {
        $colors: #FF0000 #ff8622 #FFA800 #78CE3C #00B67A;
        @return if($num <= length($colors), nth($colors, $num), #000);
    }

    @for $i from 1 to 5 {
        &[data-rating^='#{$i}'] span:nth-child(-n+#{$i}) {
            border-color: stars-color($i);
            background: stars-color($i);
        }

        &[data-rating^='#{$i}.5'] span:nth-child(#{$i+1}) {
            border-left-color: stars-color($i);
        }

        &[data-rating="4.5"] span:nth-child(-n+#{$i}) {
            border-color: stars-color(5);
            background: stars-color(5);
        }
    }


    &[data-rating="4.5"] span:nth-child(5) {
        border-left-color: stars-color(5);
    }

    &[data-rating="5"] span {
        background: stars-color(5);
        border-color: stars-color(5);
    }
}

.c-star-rating-container {
    &__title,
    &__subtext {
        @extend .u-text-sm;
    }

    &__title {
        @extend .u-text-bold;
        text-transform: uppercase;
        line-height: $xs-lh;
    }

    &__subtext {
        margin-top: 6px;
    }
}


