.c-ordered-list {
    margin-left: 16px;
    padding: 0;

    li {
        margin: 0 0 16px 0;
        padding-left: 4px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__sublist {
        margin: 0;
        padding-left: 14px;

        &:first-child {
            margin-top: 16px;
        }

        li {
            padding-left: 6px;
            margin-left: 0;

            &::marker {
                content: '\2022';
            }
        }
    }

    &--counter {
        @include reset-list;
        counter-reset: li;

        li {
            position: relative;
            counter-increment: li;
            font-size: $lg-fs;
            margin-bottom: 54px;
            padding-left: 57px;

            &::before {
                position: absolute;
                left: 0;
                display: inline-block;
                content: counter(li);
                text-align: center;
                font-size: 56px;
                font-weight: $font-weight-bold;
                line-height: $xs-lh;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}


