.c-icon-item-list {
    @include reset-list;

    & > * {
        margin-bottom: 4px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__item {
        display: block;
    }

    &__link {
        position: relative;
        z-index: 1;
        display: block;
        text-align: left;
        padding: 2px 16px 2px 40px;
        color: $primary-text;
        line-height: $md-lh;
        border: none;
        outline: none;
        background: none;

        @include hover-focus {
            color: $primary-text;
        };

        &:hover,
        &:focus,
        &:active {
            background: $info-steel-fill;
        }

        .c-ico {
            position: absolute;
            @include center-vertical-position;
            left: 14px;
        }
    }

    &--invert {
        margin: 0 -16px;
    }
}


