$row-gaps: (
    "xs": $row-gap-xs,
    "sm": $row-gap-sm,
    "md": $row-gap-md,
    "lg": $row-gap-lg,
    "xl": $row-gap-xl,
    "2xl": $row-gap-2xl,
    "3xl": $row-gap-3xl,
    "4xl": $row-gap-4xl,
    "5xl": $row-gap-5xl
);

$row-justify: (
    "center": center,
    "between": space-between,
    "start": flex-start,
    "end": flex-end,
    "evenly": space-evenly
);

$row-align: (
    "center": center,
    "around": space-around,
    "top": flex-start,
    "bottom": flex-end
);

@mixin gap($property, $value) {
    #{$property}: $value;
}

@mixin margin($direction, $value) {
    @if $direction != '' {
        & > * {
            margin-#{$direction}: $value;
        }
    } @else {
        & > * {
            margin-right: $value;
            margin-bottom: $value;
        }
    }
}

.c-row {
    display: flex;
    flex-wrap: wrap;

    &--flex {
        flex: 1;
    }

    &--nowrap {
        flex-wrap: nowrap;
    }

    &--reverse {
        flex-direction: row-reverse;
    }

    &--odd-column-gap-xl {
        & > *:nth-child(odd) {
            margin-top: $row-gap-xl;
        }
    }

    @each $name, $value in $row-justify {
        &--justify-#{$name} {
            justify-content: $value;
        }
    }

    @each $name, $value in $row-align {
        &--align-#{$name} {
            align-items: $value;
        }
    }

    @each $name, $size in $row-gaps {
        &--gap-#{$name}.c-row--by-2 > *,
        &--row-gap-#{$name}.c-row--by-2 > *,
        &--gap-#{$name}.c-row--by-3 > *,
        &--row-gap-#{$name}.c-row--by-3 > *,
        &--gap-#{$name}.c-row--by-4 > *,
        &--row-gap-#{$name}.c-row--by-4 > * {
            width: initial;
            max-width: initial;
            min-width: initial
        }

        &--gap-#{$name}.c-row--by-2 > *,
        &--row-gap-#{$name}.c-row--by-2 > * {
            flex: 0 1 calc(50% - (#{$size} / 2));
        }

        &--gap-#{$name}.c-row--by-3 > *,
        &--row-gap-#{$name}.c-row--by-3 > * {
            flex: 0 1 calc(33.333% - (#{$size} * 2 / 3));
        }

        &--gap-#{$name}.c-row--by-4 > *,
        &--row-gap-#{$name}.c-row--by-4 > * {
            flex: 0 1 calc(25% - (#{$size} * 3 / 4));
        }
    }

    @each $name, $size in $row-gaps {
        &--gap-#{$name} {
            @include margin('', $size);
            @supports (gap: 1px) {
                & > * {
                    margin-right: 0;
                    margin-bottom: 0;
                }
                @include gap(gap, $size);
            }
        }

        &--column-gap-#{$name} {
            @include margin('right', $size);
            @supports (row-gap: 1px) {
                & > * {
                    margin-right: 0;
                }
                @include gap(row-gap, $size);
            }
        }

        &--row-gap-#{$name} {
            @include margin('bottom', $size);
            @supports (column-gap: 1px) {
                & > * {
                    margin-bottom: 0;
                }
                @include gap(column-gap, $size);
            }
        }
    }
}
