.c-top-index {
    position: relative;
    top: -6px;
    vertical-align: baseline;
    color: $accent-secondary-text;
    font-size: $_3xs-fs;
    line-height: 0;
    margin: 0 3px;

    &--light {
        color: $light-text;
    }

    &--primary {
        color: $primary-text;
    }
}


