.c-top-loader {
    width: 100%;
    height: 4px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    background-color: $light-fill;
    border-radius: 30px;
    box-shadow: $box-shadow-default;

    &__indicator {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        background-color: $info-fill;
        border-radius: 30px;
        animation: top-loader-loading 1s linear infinite;
    }
}

@keyframes top-loader-loading {
    0% {
        left: -100%;
    }
    100% {
        left: 100%;
    }
}


