.c-text-editor {
    display: flex;
    flex-direction: column;
    flex: 1;
    border: 1px solid $service-border;
    background: $light-fill;

    &__content {
        flex: 1;
        padding: 16px;
        border-bottom: 1px solid $service-border;
    }

    &__textarea {
        height: 302px;
        overflow-x: hidden;
        overflow-y: auto;
        @extend .u-scrollbar-secondary;

        span {
            @include selection {
                background-color: $info-fill;
                color: $light-text;
            };
        }
    }

    &__placeholder {
        @extend .u-text-help;
    }

    &__toolbar {
        padding: 4px 8px;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        align-items: center;
    }

    .c-text-editor__btn {
        min-width: 32px;
        height: 32px;
        padding: 4px;
        background: transparent;
        border-color: transparent;
        color: $accent-text;

        &:not(:disabled),
        &:not(.is-disabled) {
            &:hover,
            &:focus,
            &:active,
            &.is-active {
                border-color: transparent;
                background-color: $info-steel-fill;
            }
        }
    }

    &__cell {
        position: relative;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        align-items: center;
        padding: 0 6px;

        &::after {
            position: absolute;
            right: -1px;
            @include center-vertical-position;
            content: '';
            display: block;
            width: 1px;
            height: 22px;
            background: $service-fill;
        }

        &:first-child {
            padding-left: 0;
        }

        &:last-child {
            padding-right: 0;

            &::after {
                display: none;
            }
        }

        & > * {
            margin-right: 4px;

            &:last-child {
                margin-right: 0;
            }
        }
    }
}


