.c-info-preview {
    position: relative;
    min-height: 108px;
    font-size: $sm-fs;
    background: $light-fill;
    border: 1px solid $service-border;
    outline: 1px solid transparent;
    outline-offset: -2px;
    border-radius: 6px;
    @extend .c-info-preview--md;
    box-shadow: $box-shadow-default;

    &:hover,
    &:focus-visible {
        @include  border-outline-color($info-border);
    }

    ul {
        @extend .c-list;
    }

    &__count,
    &__tools {
        position: absolute;
    }

    &__count {
        left: 0;
        top: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 105px;
        height: 36px;
        font-size: $xl-fs;
        font-weight: $font-weight-bold;
        color: $service-text;
    }

    &__tools {
        @include center-vertical-position;
        right: 24px;
    }

    &__title {
        @extend .c-h4;
        margin: 0 0 8px 0;
    }

    &__subtitle {
        margin-bottom: 6px;
    }

    &__content {
        max-height: 63px;
        overflow: hidden;
        margin-bottom: 13px;

        &.is-expanded {
            overflow: visible;
            max-height: 100%;
            transition: max-height .3s ease-in-out;
        }

        p {
            margin: 0;
        }
    }

    &__footer {
        display: flex;
        align-items: center;

        .c-link {
            line-height: $md-lh;
            font-weight: $font-weight-bold;

            .c-ico {
                margin: -3px 8px 0 0;
            }
        }

        .c-separator-line:first-child {
            display: none;
        }
    }

    &__form {
        width: 384px;
        margin-bottom: 24px;
    }

    &--md,
    &--md-narrow{
        padding: 23px 312px 23px 105px;
    }

    &--md-narrow {
        padding-right: 105px;
    }

    &--position-tools {
        .c-info-preview__tools {
            top: 23px;
            transform: translateY(0);
        }
    }
}


