$preview-item-sizes: (
    "xs": (136px, 175px, 0.17448, 0.17683),
    "xs-normal": (176px, 229px, 0.22656, 0.23069),
    "sm": (240px, 337px, 0.30990, 0.34045),
    "sm-normal": (280px, 364px, 0.36198, 0.36789)
);

.c-preview-box {
    position: relative;
    text-align: initial;
    cursor: pointer;

    &:hover,
    &:focus {
        outline: none;

        .c-preview-box__item {
            border-color: $info-border;
        }
    }

    &.is-active,
    &:active {
        .c-preview-box__item {
            box-shadow: none;
        }
    }

    &.is-active {
        .c-preview-box__item {
            border-color: $primary-border;
            border-width: 2px;
        }
    }

    &:active {
        .c-preview-box__item {
            border-color: $primary-border;
        }
    }

    &__title {
        color: $accent-secondary-text;
        font-size: $sm-fs;
        font-weight: $font-weight-bold;
        margin-top: 8px;
    }

    &__item {
        position: relative;
        width: 384px;
        height: 492px;
        border: 1px solid $service-border;
        border-radius: 6px;
        box-shadow: $box-shadow-default;
        overflow: hidden;
        margin: 0 auto;

        & > *:not(.c-preview-box__tag) {
            width: 768px;
            height: 984px;
            transform-origin: 0 0;
            transform: scale(0.49740, 0.49797);
        }
    }

    &__tag {
        position: absolute;
        z-index: 1;
        right: 0;
        bottom: 24px;
        font-size: $_2xs-fs;
        font-weight: $font-weight-bold;
        color: $light-text;
        line-height: $sm-lh;
        text-transform: uppercase;
        background: $success-dark-fill;
        border-radius: 4px 0 0 4px;
        padding: 2px 6px;
    }

    @each $size, $values in $preview-item-sizes {
        &--#{$size} {
            .c-preview-box__item {
                width: nth($values, 1);
                height: nth($values, 2);

                & > *:not(.c-preview-box__tag) {
                    transform: scale(nth($values, 3), nth($values, 4));
                }
            }
        }
    }

    &--no-effects {
        cursor: auto;

        &:hover,
        &:focus {
            .c-preview-box__item {
                border-color: $service-border;
            }
        }

        &:active {
            .c-preview-box__item {
                box-shadow: $box-shadow-default;
            }
        }
    }

    &--xs {
        &.c-preview-box--check-view::before {
            top: 11px;
            right: 11px;
        }

        .c-preview-box__tag {
            bottom: 16px;
        }
    }

    &--dynamic-height {
        .c-preview-box__item {
            overflow: visible;
            max-height: calc(100vh - 200px);

            & > * {
                height: auto;
                min-height: 984px;
            }
        }
    }

    &--check-view {
        &::before {
            position: absolute;
            display: none;
            content: '';
            top: 17px;
            right: 17px;
            @include bg-svg($check-circle);
            background-color: $light-fill;
            z-index: 2;
            border-radius: 50%;
        }

        &.is-active {
            &::before {
                display: inline-block;
            }
        }
    }

    &--outline-secondary {
        &:hover .c-preview-box__item,
        &:focus .c-preview-box__item,
        .c-preview-box__item {
            border-color: $primary-border;
        }

        .c-preview-box__item,
        .c-preview-box__item > *:not(.c-preview-box__tag) {
            border-radius: 0;
        }

        .c-preview-box__item {
            box-shadow: none;
        }
    }

    &--no-decor {
        .c-preview-box__item {
            border: none;
            border-radius: 0;
            box-shadow: none;
        }
    }
}


