.c-step-offset {
    & > *:nth-child(odd) {
        margin-right: 104px;
    }

    & > *:nth-child(even) {
        margin-left: 104px;
    }

    &__item {
        margin-bottom: 80px;

        &:last-child {
            margin-bottom: 0;
        }

        & > * {
            margin-top: 0;
            margin-bottom: 16px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}
