$section-sizes: (
    "xs": 10px,
    "sm": 16px,
    "md": 32px
);

.c-separate-section {
    padding: 24px 0;
    border-bottom: 1px solid $secondary-border;

    &:first-child {
        padding-top: 0;
    }

    &:last-child:not(.c-separate-section--reverse) {
        padding-bottom: 0;
        border-bottom: 0;
    }

    @each $size, $value in $section-sizes {
        &--#{$size} {
            padding: $value 0;
        }
    }

    &--top-sm {
        padding-top: 16px;
    }

    &--lg {
        padding: 40px 0;
    }

    &--reverse {
        border-top: 1px solid $secondary-border;

        &:first-child {
            border-top: 0;
        }

        &:not(:last-child) {
            border-bottom: 0;
        }
    }
}


