.c-title-collapse {
    border-bottom: 1px solid $primary-border;
    overflow: hidden;

    &:first-child {
        .c-title-collapse__toggle {
            padding-top: 0;
        }
    }

    &__toggle {
        position: relative;
        width: 100%;
        min-height: 106px;
        border: none;
        outline: none;
        background: none;
        font-size: $lg-fs;
        font-weight: $font-weight-bold;
        text-align: left;
        padding: 16px 32px 16px 0;
        margin-bottom: -16px;
        transition: margin-bottom .25s;

        &.collapsed {
            margin-bottom: 0;
        }

        &::before {
            position: absolute;
            display: inline-block;
            content: '\002B';
            @include center-vertical-position;
            right: 0;
            color: $info-text;
            font-size: 30px;
            font-weight: $font-weight-regular;
        }

        &[aria-expanded="true"] {
            &::before {
                content: "\2212";
            }
        }
    }

    &__dialog {
        position: relative;
        z-index: 1;
    }

    &__inner {
        padding-bottom: 40px;
    }
}
