.c-badge {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 29px;
    height: 28px;
    font-size: $md-fs;
    font-weight: $font-weight-bold;
    color: $light-text;
    border-radius: 4px;
    background: $danger-fill;

    &--warning {
        background: $warning-secondary-fill;
    }

    &--secondary {
        background: $secondary-dark-fill;
        color: $primary-text;
    }
}
