.c-switch {
    display: flex;

    &:hover {
        .c-switch__input:checked ~ .c-switch__toggle,
        .c-switch__toggle {
            border-color: $info-border;

            &::after {
                background-color: $info-fill;
            }
        }
    }

    &:active {
        .c-switch__input:checked ~ .c-switch__toggle,
        .c-switch__toggle {
            border-color: $primary-border;

            &::after {
                background-color: $primary-fill;
            }
        }
    }

    &__btn {
        display: inline-block;
        position: relative;
        width: 48px;
        height: 32px;
    }

    &__input {
        @include invisible-input;

        &:checked ~ .c-switch__toggle {
            &::after {
                background-color: $primary-fill;
                left: calc(100% - 6px);
                transform: translate(-100%, -50%);
            }
        }
    }

    &__toggle {
        position: absolute;
        @include full-position;
        background-color: $light-fill;
        border: 1px solid $service-border;
        border-radius: 30px;
        transition: .5s;
        cursor: pointer;

        &::after {
            content: "";
            position: absolute;
            @include center-vertical-position;
            left: 6px;
            width: 18px;
            height: 18px;
            border-radius: 50%;
            background-color: $service-fill;
            transition: .3s ease-out;
        }
    }
}


