.c-btn-toggler {
    position: relative;
    display: inline-block;
    background: none;
    outline: none;
    padding: 0 20px 0 0;
    border: 0;
    font-size: $sm-fs;
    color: $primary-text;
    font-weight: $font-weight-bold;
    cursor: pointer;

    &:hover,
    &:focus-visible {
        color: $info-text;

        &::after {
            text-decoration: underline;
        }
    }

    .c-ico {
        position: absolute;
        right: 0;
    }

    &::after {
        display: inline-block;
        content: attr(data-active-text);
    }

    &.is-hidden {
        &::after {
            content: attr(data-text);
        }

        .c-ico {
            transform: rotate(-180deg);
        }
    }
}


