.c-tooltip {
    position: relative;
    display: block;
    width: 384px;
    padding: 24px;
    margin: 0 0 0 26px;
    font-size: $md-fs;
    color: $primary-text;
    border: 1px solid $service-border;
    background: $light-fill;
    box-shadow: $box-shadow-default;
    z-index: 10;

    &::before,
    &::after {
        content: '';
        display: block;
        position: absolute;
        @include center-vertical-position;
        border: 18px solid transparent;
    }

    &:before {
        border-right: 18px solid $service-border;
        left: -36px;
    }

    &::after {
        border-right: 19px solid $light-border;
        left: -35px;
    }

    &__close {
        margin: -15px -15px 0 0;
    }

    &--space-sm {
        padding: 16px;
    }

    &--sm {
        width: 280px;
    }

    &--md {
        width: 488px;
    }

    &--full {
        width: 100%;
    }

    &--arrow-left-top {
        &::before,
        &::after {
            top: 0;
            transform: translateY(16px);
        }
    }

    &--arrow-top-center,
    &--arrow-top-right {
        margin: 26px 0 0 0;

        &::before,
        &::after {
            top: -36px;
            border: 18px solid transparent;
        }

        &::before {
            top: -37px;
            border-bottom: 18px solid $service-border;
        }

        &::after {
            border-bottom: 18px solid $light-border;
        }
    }

    &--arrow-top-center {
        &::before,
        &::after {
            left: 50%;
            transform: translateX(-50%);
        }
    }

    &--arrow-top-right,
    &--arrow-bottom-right {
        &::before,
        &::after {
            left: auto;
            right: 16px;
            transform: translateY(0);
        }
    }

    &--arrow-bottom-right {
        margin: 0 0 26px 0;

        &::before,
        &::after {
            top: auto;
            bottom: -36px;
            border: 18px solid transparent;
        }

        &::before {
            bottom: -37px;
            border-top: 18px solid $service-border;
        }

        &::after {
            border-top: 18px solid $light-border;
        }
    }

    &--arrow-bottom-right,
    &--arrow-bottom-right.c-tooltip--secondary {
        box-shadow: $box-shadow-2xl;
    }

    &--secondary {
        border: none;
        border-radius: 6px;
        box-shadow: $box-shadow-4xl;

        &::before {
            border-color: transparent;
        }
    }
}

.c-tooltip-box {
    position: relative;
    display: inline-block;

    &--hover-show:hover,
    &.is-open {
        .c-tooltip {
            display: block;
        }
    }

    &:hover,
    &--hover-show:hover,
    &.is-open {
        .c-tooltip-box__toggle,
        .c-tooltip-box__toggle .c-ico {
            color: $info-text
        }
    }

    &:hover,
    &.is-open {
        .c-tooltip-box__toggle {
            .c-ico {
                &:first-child:not(:only-child) {
                    display: none;
                }

                &:last-child {
                    display: inline-block;
                }
            }
        }
    }

    &__toggle {
        position: relative;
        @extend .c-link;
        font-weight: $font-weight-bold;
        color: $accent-secondary-text;
        min-height: 24px;
        padding-left: 30px;
        cursor: pointer;

        &:hover,
        &:focus {
            text-decoration: none;
        }

        .c-ico {
            position: absolute;
            top: 0;
            left: 0;

            &:last-child:not(:only-child) {
                display: none;
            }
        }
    }

    .c-tooltip {
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        left: 100%;
        display: none;
    }

    &--top-right {
        .c-tooltip {
            top: auto;
            bottom: 100%;
        }
    }

    &--bottom-center,
    &--bottom-right {
        .c-tooltip {
            top: 100%;
        }
    }

    &--top-right,
    &--bottom-right {
        .c-tooltip {
            left: auto;
            right: 0;
            transform: translate(0, 0);
        }
    }

    &--bottom-center {
        .c-tooltip {
            left: 50%;
            transform: translate(-50%, 0);
        }
    }

    & > .c-ico {
        cursor: pointer;
    }
}
