.c-step-content {
    display: flex;
    align-items: center;
    width: 1153px;
    padding: 0 40px;

    &__container {
        display: flex;
        width: 553px;
        margin-right: 69px;
    }

    &__marker {
        font-size: 56px;
        font-weight: $font-weight-bold;
        line-height: $xs-lh;
        margin-right: 32px;
    }

    &__text {
        font-size: $lg-fs;
        text-align: left;
    }

    &__img {
        min-width: 436px;
        text-align: center;
    }

    .c-h3-md {
        margin-bottom: 24px;
    }

    &--reverse {
        flex-direction: row-reverse;

        .c-step-content__container {
            margin-left: 69px;
            margin-right: 0;
        }
    }

    &--column-reverse {
        flex-direction: column-reverse;
        height: 100%;
        padding: 0;

        .c-step-content__container {
            margin: 0;
            text-align: center;
        }

        .c-step-content__img {
            display: flex;
            align-items: center;
            justify-content: center;
            min-width: auto;
            margin-bottom: 24px;
        }
    }

    &--xs,
    &--sm {
        .c-step-content__container {
            width: auto;
        }
    }

    &--xs {
        width: 280px;

        .c-step-content__img {
            height: 175px;
        }
    }

    &--sm {
        width: 382px;

        .c-step-content__img {
            height: 244px;
        }
    }


    &--align-start {
        align-items: flex-start;

        .c-step-content__container {
            text-align: left;
        }
    }
}
