.c-image-box {
    max-width: 248px;
    width: 100%;

    &:hover,
    &:focus-visible {
        .c-image-box__img {
            border-color: $info-border;
            cursor: pointer;
        }
    }

    &__img {
        border: 1px solid $service-border;
        border-radius: 6px;
        box-shadow: $box-shadow-default;
        overflow: hidden;
        width: inherit;
        margin-bottom: 8px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__title {
        @extend .u-text-sm;
        @extend .u-text-help;
        @extend .u-text-single-line;
    }
}


