.c-input-control {
    display: block;

    &:hover,
    &:focus {
        .c-input-control__input[type="checkbox"],
        .c-input-control__input[type="radio"] {
            &:not(:disabled) {
                & + .c-input-control__label::before {
                    border-color: $info-border;
                }
            }
        }
    }

    &:active {
        .c-input-control__input[type="checkbox"],
        .c-input-control__input[type="radio"] {
            &:not(:disabled) {
                & + .c-input-control__label::before {
                    border-color: $primary-border;
                }
            }
        }
    }

    &__input {
        @include invisible-input;
    }

    &--theme-checkbox > .c-input-control__input[type="radio"],
    &__input[type="checkbox"],
    &__input[type="radio"] {
        &:disabled {
            & + .c-input-control__label {
                cursor: none;
                pointer-events: none;
                opacity: .5;
            }
        }

        &:checked {
            & + .c-input-control__label::after {
                opacity: 1;
            }
        }
    }

    &--theme-checkbox > .c-input-control__input[type="radio"],
    &__input[type="checkbox"] {
        & + .c-input-control__label::after {
            left: 0;
            top: 0;
            @include bg-svg($check);
        }
    }

    &__input[type="radio"] {
        & + .c-input-control__label {
            &::before {
                border-radius: 50%;
            }

            &::after {
                left: 6px;
                top: 6px;
                @include bg-svg($ellipse);
            }
        }
    }

    &--theme-checkbox > .c-input-control__input[type="radio"] {
        & + .c-input-control__label {
            &::before {
                border-radius: 0;
            }
        }
    }

    &__label {
        position: relative;
        display: inline-block;
        min-width: 24px;
        min-height: 24px;
        padding-left: 40px;
        cursor: pointer;

        &::before,
        &::after {
            content: '';
            display: inline-block;
            position: absolute;
        }

        &::before {
            top: 0;
            left: 0;
            width: 24px;
            height: 24px;
            border: 1px solid $service-border;
            background: $light-fill;
        }

        &::after {
            opacity: 0;
        }

        &:empty {
            padding-left: 24px;
        }
    }
}


