.c-overlay-loader {
    position: fixed;
    @include full-position;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    background: $secondary-fill;
    padding: 16px;

    &__container {
        width: 592px;
    }

    &__img {
        display: block;
        margin-bottom: 26px;
    }

    .c-loader,
    &__progress {
        margin-bottom: 30px;
    }


    &__progress {
        position: relative;
        width: 100%;
        height: 8px;
        background-color: $light-fill;
        border-radius: 30px;
        box-shadow: $box-shadow-default;
    }

    &__indicator {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $info-fill;
        border-radius: 30px;
        animation: overlay-loader-width 2s forwards;
        z-index: 3;
    }

    &__list {
        @include reset-list;
    }

    &__title,
    &__subtitle,
    &__item {
        font-weight: $font-weight-bold;
    }

    &__subtitle,
    &__item {
        font-size: $lg-fs;
    }

    &__title {
        font-size: $xl-fs;
        margin-bottom: 21px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        white-space: nowrap;
        margin-bottom: 10px;

        &:not(:last-child) {
            .c-overlay-loader__text {
                animation: overlay-loader-list .5s linear forwards;
            }

            .c-ico {
                animation: overlay-loader-list .17s linear forwards;

            }
        }

        .c-ico {
            opacity: 0;
            margin-right: 16px;
            animation: overlay-loader-list .34s linear .5s forwards;
        }
    }

    &__text {
        opacity: 0;
        animation: overlay-loader-list .33s linear .8s forwards;
    }

    &--content-center {
        text-align: center;
    }

    &--sm {
        .c-overlay-loader__img {
            margin-top: -32px;
            margin-bottom: 0;
        }

        .c-loader {
            margin-bottom: 20px;
        }
    }
}

@keyframes overlay-loader-width {
    0% {
        width: 0;
    }

    100% {
        width: 100%;
    }
}

@keyframes overlay-loader-list {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}


