$ico-sizes: (
    "xxs": $xxs-ico,
    "xs": $xs-ico,
    "sm": $sm-ico,
    "md": $md-ico,
    "lg": $lg-ico,
    "xl": $xl-ico,
    "2xl": $_2xl-ico,
    "3xl": $_3xl-ico,
    "4xl": $_4xl-ico,
    "5xl": $_5xl-ico,
    "6xl": $_6xl-ico
);

$ico-colors: (
    "inherit": inherit,
    "primary": $primary-text,
    "light": $light-text,
    "secondary": $accent-secondary-text,
    "accent": $accent-text,
    "info": $info-text,
    "service": $service-text,
    "success": $success-text,
    "warning": $warning-text,
    "highlight": $warning-light-text,
    "danger": $danger-text
);

$ico-back-colors: (
    "info": ($info-pale-text, $info-pale-border),
    "success": ($success-pale-text, $success-pale-border),
    "warning": ($warning-pale-text, $warning-pale-border),
    "marker": ($marker-pale-text, $marker-pale-border),
    "service": ($accent-secondary-text, $service-border)
);

@mixin ico-size($size) {
    width: $size;
    height: $size;
}

.c-ico {
    display: inline-block;
    vertical-align: middle;
    font-size: 0;
    flex-shrink: 0;

    @each $name, $size in $ico-sizes {
        &--#{$name} {
            @include ico-size($size);
        }
    }

    @each $theme, $color in $ico-colors {
        &--#{$theme} {
            color: $color;
        }
    }
}

.c-ico-back {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    color: $primary-text;
    border: 1px solid $primary-border;
    border-radius: 8px;

    @each $theme, $colors in $ico-back-colors {
        &--#{$theme} {
            color: nth($colors, 1);
            border-color: nth($colors, 2);
        }
    }

    &--wide {
        width: 104px;
        height: 56px;
        border-radius: 0;
    }

    &--no-border-right {
        border-right: none;
    }
}


