.c-dropdown {
    position: relative;

    &__menu {
        display: none;
        overflow: hidden;
        position: absolute;
        background-color: $light-fill;
        border-radius: 6px;
        top: 100%;
        left: 0;
        right: 0;
        margin-top: 1px;
        margin-bottom: 1px;
        box-shadow: $box-shadow-default;

        &.show,
        &.is-active {
            display: block;
            z-index: 10;
        }
    }

    &--top,
    &--top-left,
    &--top-center,
    &--top-right,
    &--parent-top {
        .c-dropdown__menu {
            top: auto;
            bottom: 100%;
            box-shadow: $box-shadow-reverse;
        }
    }

    &--top-left,
    &--top-right,
    &--top-center,
    &--bottom-left,
    &--bottom-right,
    &--bottom-center {
        .c-dropdown__menu {
            min-width: 208px;
        }
    }

    &--top-left,
    &--bottom-left {
        .c-dropdown__menu {
            left: 0;
            right: auto;
        }
    }

    &--top-right,
    &--bottom-right {
        .c-dropdown__menu {
            right: 0;
            left: auto;
        }
    }

    &--top-center,
    &--bottom-center {
        .c-dropdown__menu {
            @include center-horizontal-position;
            right: auto;
        }
    }

    &--parent-top,
    &--parent-bottom {
        position: inherit;

        .c-dropdown__menu {
            border-radius: 0;
        }
    }

    &--invisible {
        .c-dropdown__menu {
            overflow: initial;
            background-color: transparent;
            border-radius: 0;
            box-shadow: none;
        }
    }

    &--hover-visible {
        &:hover {
            .c-dropdown__menu {
                display: block;
                z-index: 10;
                margin: 0;
            }
        }

        .c-dropdown__menu {
            margin: 0;
        }
    }

    &--link-sm {
        .c-dropdown__link {
            font-size: $sm-fs;
            padding-top: 9px;
            padding-bottom: 9px;
        }
    }

    &--link-sm {
        .c-dropdown__link {
            font-size: $sm-fs;
            padding-top: 9px;
            padding-bottom: 9px;
        }
    }

    &__link {
        display: block;
        width: 100%;
        padding: 12px 16px;
        color: $primary-text;
        line-height: $md-lh;
        text-align: left;
        text-decoration: none;
        white-space: nowrap;
        cursor: pointer;
        background: none;
        border: none;
        outline: none;

        @include hover-focus {
            background: $info-steel-fill;
        };

        .c-ico {
            margin: -3px 10px 0 0;
        }
    }
}


