.range-slider {
    touch-action: none;
    -webkit-tap-highlight-color: transparent;
    -webkit-user-select: none;
    user-select: none;
    cursor: pointer;
    display: block;
    position: relative;
    width: 100%;
    height: 8px;
    background: #ddd;
    border-radius: 4px;
}
.range-slider[data-vertical] {
    height: 100%;
    width: 8px;
}
.range-slider[data-disabled] {
    opacity: 0.5;
    cursor: not-allowed;
}
.range-slider .range-slider__thumb {
    position: absolute;
    z-index: 3;
    top: 50%;
    width: 24px;
    height: 24px;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background: #2196f3;
}
.range-slider .range-slider__thumb:focus-visible {
    outline: 0;
    box-shadow: 0 0 0 6px rgba(33, 150, 243, 0.5);
}
.range-slider[data-vertical] .range-slider__thumb {
    left: 50%;
}
.range-slider .range-slider__thumb[data-disabled] {
    z-index: 2;
}
.range-slider .range-slider__range {
    position: absolute;
    z-index: 1;
    transform: translate(0, -50%);
    top: 50%;
    width: 100%;
    height: 100%;
    background: #51adf6;
}
.range-slider[data-vertical] .range-slider__range {
    left: 50%;
    transform: translate(-50%, 0);
}
.range-slider input[type="range"] {
    -webkit-appearance: none;
    pointer-events: none;
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    background-color: transparent;
}
.range-slider input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
}
.range-slider input[type="range"]::-moz-range-thumb {
    width: 0;
    height: 0;
    border: 0;
}
.range-slider input[type="range"]:focus {
    outline: 0;
}

/* Range Slider as a Single Thumb Slider */
.single-thumb .range-slider__thumb[data-lower] {
    width: 0;
}
.single-thumb .range-slider__range {
    border-radius: 6px;
}

// You can add your styles:
.c-range-slider {
    height: 24px;
    border-radius: 0;
    background: transparent;

    &::before {
        position: absolute;
        content: '';
        height: 4px;
        border-radius: 8px;
        background-color: $secondary-dark-fill;
        left: 0;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
    }

    .range-slider__range {
        height: 4px;
        border-radius: 8px;
        background: $secondary-dark-fill;
    }

    .range-slider__thumb {
        background: $primary-fill;

        &[data-active] {
            background: $info-fill;
        }
    }

    &[data-vertical],
    &[data-vertical] .range-slider__range {
        height: 100%;
        width: 4px;
    }

    &[data-vertical] {
        .range-slider__thumb,
        .range-slider__range {
            top: 0;
        }

        .range-slider__range {
            left: 0;
            transform: none;
        }
    }

    &--thumb-sm {
        .range-slider__thumb {
            width: 16px;
            height: 16px;
        }
    }

    &--vertical {
        &::before {
            width: 4px;
            height: 100%;
            top: 0;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}

.c-range-slider-controls {
    position: relative;

    &.is-disabled {
        opacity: .5;
    }

    &__markers {
        position: absolute;
        display: flex;
        justify-content: space-between;
        left: 0;
        right: 0;
        bottom: 8px;

        &::before {
            position: absolute;
            content: '';
            height: 4px;
            border-radius: 8px;
            background-color: $secondary-dark-fill;
            left: 0;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
        }

        & + .c-range-slider-controls__range {
            padding-top: 46px;
        }
    }

    &__point {
        position: relative;
        width: 8px;
        height: 8px;
        text-align: center;
        background: #5E718C;
        border-radius: 50%;
    }

    &__label {
        position: absolute;
        font-size: 13px;
        left: 50%;
        transform: translateX(-50%);
        bottom: 100%;
        min-width: 108px;
        max-width: 150px;
        margin-bottom: 12px;
    }

    &__range {
        position: relative;
        z-index: 3;

        .c-range-slider {
            &::before {
                display: none;
            }

            .range-slider__range {
                height: 0;
                border-radius: 0;
                background: transparent;
            }

            &.range-slider[data-disabled] {
                opacity: 1;
            }
        }
    }
}


