.c-section {
    padding: 120px 0;
    color: $primary-text;
    background: $light-fill;

    &--no-padding {
        padding: 0;
    }

    &--md-wide {
        padding: 132px 0 120px;
    }

    &--md-narrow {
        padding: 132px 0 80px;
    }

    &--sm {
        padding: 70px 0 52px;
    }

    &--xs {
        padding: 64px 0;
    }

    &--xxs {
        padding: 56px 0;
    }

    &--secondary {
        background: $secondary-fill;
    }

    &--success,
    &--info {
        color: $light-text;
    }

    &--success {
        background: $success-fill;
    }

    &--info {
        background: $info-light-fill;
    }
}


