.c-list-card {
    @include reset-list;

    p {
        margin: 0;
    }

    &__item {
        display: flex;
        align-items: center;
        padding: 16px;
        background: $light-fill;
        border: 1px solid $service-border;
        border-radius: 6px;
        box-shadow: $box-shadow-default;
        cursor: pointer;
        margin-bottom: 8px;

        &:last-child {
            margin-bottom: 0;
        }

        &:not(.is-active):hover,
        &:focus,
        &:not(.is-active):active {
            border-color: $info-border;

            .c-list-card__btn {
                color: $light-text;
                background: $info-fill;
                border-color: $info-border;
            }
        }

        &.is-active {
            .c-list-card__btn {
                color: $light-text;
                background: $service-fill;
                border-color: $service-border;
            }

            .c-list-card__content,
            .c-list-card__tag,
            .c-list-card__tag .c-ico {
                color: $accent-secondary-text;
            }

            .c-list-card__tag {
                opacity: .6;
            }
        }
    }

    &__btn {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 48px;
        height: 48px;
        color: $info-text;
        font-size: $sm-fs;
        font-weight: $font-weight-bold;
        line-height: $md-lh;
        padding: 4px;
        background: $light-fill;
        border: 1px solid $info-border;
        border-radius: 6px;
        outline: none;
        transition: .25s;
    }

    &__content {
        flex: 2;
        margin-left: 16px;
        @include user-select-none;

        &:first-child {
            margin-left: 0;
        }
    }

    &__tag {
        display: block;
        color: $warning-dark-text;
        font-size: $sm-fs;
        font-weight: $font-weight-bold;
        margin-bottom: 4px;

        .c-ico {
            margin: -2px 4px 0 -2px;
        }
    }

    &--flex {
        display: flex;
        flex-wrap: wrap;

        .c-list-card__item {
            margin-right: 10px;
            margin-bottom: 10px;
        }

        @supports (gap: 1px) {
            gap: 10px;

            .c-list-card__item {
                margin-right: 0;
                margin-bottom: 0;
            }
        }
    }
}


