.c-option-large-card {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    font-size: $md-fs;
    line-height: $md-lh;
    color: $primary-text;
    border: 1px solid $service-border;
    border-radius: 6px;
    background-color: $light-fill;
    outline: 1px solid transparent;
    outline-offset: -2px;
    box-shadow: $box-shadow-default;
    @extend .c-option-large-card--md;

    &:not(:disabled),
    &:not(.is-disabled) {
        @include hover-focus {
            border-color: $info-border;
        };

        &.is-active,
        &:active {
            box-shadow: none;

            .c-option-large-card__text {
                font-weight: $font-weight-regular;
            }
        }

        &:active {
            border-color: $primary-border;
        }

        &.is-active {
            font-weight: $font-weight-bold;
            @include border-outline-color($primary-border);
        }
    }

    &:disabled,
    &.is-disabled {
        color: $accent-secondary-text;
        border-color: $secondary-light-border;
        outline-color: transparent;
        pointer-events: none;
        box-shadow: none;

        .c-ico {
            color: $accent-secondary-text;
        }
    }

    &__title,
    &__icon {
        margin-bottom: 14px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__title {
        font-size: 24px;
        font-weight: $font-weight-bold;
    }

    &__icon {
        color: $info-text;

        .c-ico {
            width: 80px;
            height: 80px;
        }
    }

    &--md {
        padding: 54px 38px;
    }

    &--md-narrow {
        padding-top: 40px;
        padding-bottom: 40px;
    }
}

