.c-toast {
    position: fixed;
    top: 16px;
    @include center-horizontal-position;
    z-index: 1050;

    &:not(.show) {
        display: none;
    }

    &__close {
        position: absolute;
        top: 5px;
        right: 10px;
        color: $light-text;

        & + .c-toast__content {
            padding-right: 60px;
        }
    }

    &__content {
        padding: 12px 24px;
        color: $light-text;
        font-weight: $font-weight-bold;
        background: $info-fill;
        border-radius: 24px;
    }

    &--danger {
        .c-toast__content {
            background: $danger-fill;
        }
    }
}




