.c-navbar {
    display: flex;
    align-items: center;

    & > * {
        margin-right: 16px;

        &:last-child {
            margin-right: 0;
        }
    }

    &__item {
        font-size: $sm-fs;
        font-weight: $font-weight-bold;
        color: $primary-text;
        padding: 10px 16px;
        border-radius: 6px;
        transition: background .2s ease-in-out;

        &:hover,
        &:focus,
        &.is-active {
            background: $secondary-fill;
        }

        &:hover,
        &:focus {
            text-decoration: none;
        }
    }
}


