.c-outline-box {
    padding: 56px 44px;
    border-radius: 8px;
    border: 1px solid $light-border;
    background: transparent;

    &--secondary {
        border-color: $secondary-border;
    }

    &--light {
        background: $light-fill;
        border-color: $primary-border;
    }

    &--accent {
        border-color: $accent-steel-border;
    }

    &--fill {
        background: $light-fill;
    }

    &--sm {
        padding: 30px 36px;
    }

    &--md-wide {
        padding-left: 104px;
        padding-right: 104px;
    }

    &--xs {
        padding: 24px;
    }
}


