.c-bottom-panel {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    padding: 16px 0;
    z-index: 10;
    background: $light-fill;

    &__title,
    &__name {
        font-size: $md-fs;
    }

    &__title {
        max-width: 336px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    &__name {
        color: $accent-secondary-text;
        font-weight: $font-weight-bold;
    }
}


