.c-progress-bar {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: center;

    a.c-progress-bar__step,
    button.c-progress-bar__step {
        cursor: pointer;

        &:hover,
        &:focus-visible  {
            .c-progress-bar__text {
                color: $info-text;
                text-decoration: underline;
            }
        }
    }

    &__step {
        position: relative;
        display: inline-block;
        min-width: 130px;
        padding: 0 10px;
        font-size: 0;
        background: none;
        border: none;
        outline: none;
        z-index: 1;
        @include hover-focus;

        &:first-child {
            padding-left: 0;
        }

        &:last-child {
            padding-right: 0;
            min-width: auto;

            &::after {
                display: none;
            }
        }

        &::after,
        &.is-completed .c-progress-bar__arrow::after,
        &.is-visited .c-progress-bar__text::after {
            content: "";
            position: absolute;
        }

        &::after {
            top: 11px;
            right: 0;
            left: 36px;
            height: 1px;
            width: 100%;
            background: $help-fill;
            z-index: -2;
        }

        &.is-active,
        &.is-completed {
            .c-progress-bar__arrow,
            .c-progress-bar__arrow::before {
                background: $success-light-fill;
            }
        }

        &.is-active {
            .c-progress-bar__arrow,
            .c-progress-bar__text {
                font-weight: $font-weight-bold;
            }

            .c-progress-bar__arrow {
                color: $light-text;
            }

            .c-progress-bar__text {
                color: $primary-text;
            }
        }

        &.is-completed {
            .c-progress-bar__arrow {
                color: transparent;

                &::after {
                    @include center-position;
                    @include bg-svg($check-ligh-sm);
                }
            }
        }

        &.is-visited {
            .c-progress-bar__text {
                color: $info-text;
                padding-left: 20px;

                &::after {
                    left: 10px;
                    @include bg-svg($alert-rhombus-info);
                }
            }
        }
    }

    &__text,
    &__arrow {
        font-size: $xs-fs;
    }

    &__text {
        color: $accent-secondary-text;
        margin-top: 8px;
    }

    &__arrow,
    &__arrow::before {
        background: $help-fill;
    }

    &__arrow {
        position: relative;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        flex-wrap: nowrap;
        height: 24px;
        min-width: 29px;
        color: $accent-text;
        font-weight: $font-weight-bold;
        line-height: $xs-lh;
        margin-right: 10px;
        padding: 4px;
        border-radius: 4px;
        border-right: 0;

        &::before {
            content: "";
            display: inline-block;
            position: absolute;
            top: 50%;
            right: 0;
            width: 19px;
            height: 19px;
            border-radius: 4px;
            margin: 0 -7px 0 0;
            transform: translateY(-50%) rotate(45deg);
            z-index: -1;
        }
    }

    &--md {
        .c-progress-bar__img {
            margin-bottom: 24px;
        }

        .c-progress-bar__step {
            &::after {
                height: 2px;
            }
        }

        .c-progress-bar__arrow {
            height: 37px;
            min-width: 42px;
            font-size: $md-fs;
            margin-right: 15px;

            &::before {
                width: 27px;
                height: 27px;
                margin: 0 -11px 0 0;
            }
        }

        .c-progress-bar__text {
            font-size: $md-fs;
            margin-top: 14px;
        }
    }

    &--long {
        padding: 8px 0;
    }

    &--illustration {
        justify-content: space-between;

        .c-progress-bar__arrow,
        .c-progress-bar__arrow::before {
            background: $success-light-fill;
        }

        .c-progress-bar__arrow {
            color: $light-text;
        }

        .c-progress-bar__step,
        .c-progress-bar__img {
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .c-progress-bar__step {
            flex-direction: column;
            min-width: 140px;
            text-align: center;

            &::after {
                display: none;
            }
        }

        .c-progress-bar__img {
            flex: 1;
        }

        .c-progress-bar__text {
            max-width: 272px;
            color: $primary-text;
            font-weight: $font-weight-bold;
        }
    }

    &--step-horizontal {
        .c-progress-bar__step {
            min-width: 140px;
            padding: 0 16px;

            &::after {
                display: none;
            }
        }

        .c-progress-bar__text {
            display: inline-block;
            margin-top: 0;
            margin-left: 8px;
            color: $primary-text;
        }
    }

    &--secondary {
        .c-progress-bar__step {
            &::after {
                background: $service-fill;
                opacity: 0.2;
            }
        }

        .c-progress-bar__arrow,
        .c-progress-bar__arrow::before {
            background: $service-fill;
        }

        .c-progress-bar__arrow {
            color: $light-text;
        }
    }

    &--vertical {
        flex-direction: column;

        .c-progress-bar__step {
            display: flex;
            padding: 0;
            padding-bottom: 64px;

            &:last-child {
                padding-bottom: 0;
            }

            &::after {
                top: 0;
                right: auto;
                left: 17px;
                height: 100%;
                width: 1px;
            }

            &:not(:last-child) {
                .c-progress-bar__arrow {
                    height: 29px;
                    min-width: 24px;

                    &::before {
                        top: auto;
                        bottom: 0;
                        left: 50%;
                        right: auto;
                        transform: translate(-50%, 0) rotate(45deg);
                        margin: 0 0 -7px 0;
                    }
                }
            }
        }

        .c-progress-bar__arrow {
            margin-right: 66px;
        }

        &.c-progress-bar--md {
            .c-progress-bar__step {
                &::after {
                    width: 2px;
                }

                &:not(:last-child) {
                    .c-progress-bar__arrow {
                        height: 32px;
                        min-width: 37px;

                        &:before {
                            margin: 0 0 -11px 0;
                        }
                    }
                }

                &:last-child {
                    .c-progress-bar__arrow {
                        margin-right: 60px;
                    }
                }
            }
        }
    }
}


