body {
    font-family: $font-family;
    font-size: $md-fs;
    line-height: $md-lh;
}

h1, h2, h3, h4, h5, h6,
.c-h1, .c-h1-lg, .c-h2, .c-h3, .c-h3-md, .c-h4, .c-h5, .c-h6 {
    display: block;
    font-size: $md-fs;
    font-weight: $font-weight-bold;
    margin: 1em 0 .5em 0;
    font-family: $font-family;
    line-height: $md-lh;

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

h1, h2,
.c-h1, .c-h1-lg, .c-h2, .c-h3, .c-h3-md {
    line-height: $sm-lh;
}

.c-h1-lg {
    font-size: $_2xl-fs + 12px;
}

h1, .c-h1 {
    font-size: $_2xl-fs;
}

h2, .c-h2 {
    font-size: $xl-fs;
}

.c-h3-md {
    font-size: $lg-fs + 4px;
}

h3, .c-h3 {
    font-size: $lg-fs;
}

h5, .c-h5 {
    font-size: $sm-fs;
}

h6, .c-h6 {
    font-size: $xs-fs;
}

p,
ul,
ol {
    margin: 1.5em 0;

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

b, strong {
    font-weight:  $font-weight-bold;
}


