.c-color-circle {
    position: relative;
    display: inline-block;
    width: 24px;
    height: 24px;
    border: 1px solid transparent;
    border-radius: 50%;
    background-color: $light-fill;


    &--slash {
        border: 1px solid $service-border;

        &::after {
            content: '';
            position: absolute;
            width: 2px;
            height: 12px;
            top: 50%;
            left: 50%;
            background: $primary-text;
            transform: translate(-50%, -50%) rotate(24deg);
        }
    }
}


