.c-icon-button {
    display: inline-flex;
    @extend .c-icon-button--md;
    padding: 0;
    font-size: 0;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
    border-radius: 6px;
    background: transparent;
    outline: none;
    transition: .12s;

    &:hover {
        text-decoration: none;
    }

    &:active {
        transform: scale(.8);
    }

    &--md,
    &--circle-md,
    &--long-md {
        width: 40px;
        height: 40px;
    }

    &--circle-md {
        border-radius: 50%;
    }

    &--lg  {
        width: 48px;
        height: 48px;
    }

    &--long-md {
        width: 48px;
        border-radius: 20px;
    }

    &--secondary,
    &--highlight {
        &:hover,
        &:focus-visible {
            border-color: $primary-border;
        }

        &:active {
            color: $light-text;
            border-color: $accent-dark-border;
            background: $accent-dark-fill;
            transform: none;
        }
    }

    &--highlight,
    &--light {
        background: $light-fill;
    }

    &--highlight {
        box-shadow: $box-shadow-default;

        &:active {
            box-shadow: none;
        }
    }

    &--secondary-soft,
    &--light {
        outline: 1px solid transparent;
        outline-offset: -2px;

        &:hover,
        &:focus-visible {
            border-color: $info-border;
        }

        &:active,
        &.is-active {
            background: $light-fill;
            @include  border-outline-color($primary-border);
            transform: none;
        }
    }
}


