.c-post-preview {
    display: flex;
    flex-wrap: nowrap;
    align-items: flex-start;
    padding: 24px;
    border: 1px solid transparent;
    border-radius: 6px;
    background: $light-fill;
    box-shadow: $box-shadow-default;

    &:hover,
    &:focus,
    &:focus-visible,
    &:active {
        text-decoration: none;
    }

    &:hover,
    &:focus-visible,
    &:active {
        border-color: $info-border;
    }

    & > * {
        margin-right: 32px;

        &:last-child {
            margin-right: 0;
        }
    }

    &__img,
    &__content,
    &__title,
    &__text {
        display: block;
    }

    &__img {
        font-size: 0;

        img {
            display: block;
            width: 152px;
            height: auto;
        }
    }

    &__content {
        & > * {
            margin-bottom: 8px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__title,
    &__text {
        color: $primary-text;
    }

    &__title {
        @extend .c-h3;

        &:hover,
        &:focus {
            color: $info-text;
            text-decoration: underline;
        }
    }

    &__text {
        @extend .u-text-double-line;
    }
}


