@mixin style-button($text-color, $bg) {
    color: $text-color;
    background-color: $bg;
}

a.c-btn {
    &:not(:disabled),
    &:not(.is-disabled) {
        &:hover,
        &:focus {
            text-decoration: none;
        }
    }
}

.c-btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-basis: content;
    text-align: center;
    width: 100%;
    font-size: $md-fs;
    font-weight: $font-weight-bold;
    line-height: $md-lh;
    @extend .c-btn--md;
    outline: none;
    border-radius: 6px;
    border: 1px solid transparent;
    @include style-button($light-text, $primary-fill);

    &:not(:disabled),
    &:not(.is-disabled) {
        &:hover,
        &:focus-visible {
            background-color: $dark-fill;
        }

        &:active:not(.c-btn--toggle),
        &.is-active:not(.c-btn--toggle) {
            background-color: $accent-dark-fill;
        }
    }

    &:disabled,
    &.is-disabled {
        opacity: 0.4;
        pointer-events: none;
    }

    &.is-loading {
        color: transparent;

        .c-btn__indicator {
            display: block;
        }
    }

    &__ico-left {
        margin-right: 10px;
    }

    &__ico-right {
        margin-left: 10px;
    }

    &__item {
        margin-right: 16px;

        &:first-child {
            color: $accent-dark-text;
            font-weight: $font-weight-bold;
        }

        &:last-child {
            margin-right: 0;

            &:not(:only-child) {
                max-width: 133px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }

    &__indicator,
    &__indicator::before,
    &__indicator::after {
        display: inline-block;
        border-radius: 50%;
        animation: btn-loading 0.75s infinite cubic-bezier(0.13, 1.29, 0.32, 1.29);;
    }

    &__indicator {
        display: none;
        position: absolute;
        @include center-position;
        width: 10px;
        height: 10px;
        animation-delay: 0.05s;

        &::before,
        &::after {
            content: '';
            position: absolute;
            @include center-vertical-position;
            background: inherit;
        }

        &::before {
            left: -19px;
            width: 8px;
            height: 8px;
        }

        &::after {
            left: 19px;
            width: 12px;
            height: 12px;
            animation-delay: 0.10s;
        }
    }

    @keyframes btn-loading {
        0%, 100% {
            background: transparent;
        }
        50% {
            background: $light-fill;
        }
    }

    &--info {
        @include style-button($light-text, $info-fill);
    }

    &--info-light {
        @include style-button($light-text, $info-light-fill);
        border-color: $light-border;

        &:not(:disabled),
        &:not(.is-disabled) {
            &:hover,
            &:focus-visible,
            &:active,
            &.is-active {
                border-color: transparent;
            }
        }
    }

    &--secondary,
    &--link,
    &--link-secondary,
    &--ghost,
    &--light {
        .c-btn__indicator {
            background: $primary-fill;
        }
    }

    &--secondary,
    &--link,
    &--link-secondary,
    &--ghost {
        @include style-button($primary-text, transparent);
    }

    &--link,
    &--link-secondary,
    &--ghost {
        @extend .c-btn--content-start;
    }

    &--secondary,
    &--secondary-light {
        &:not(:disabled),
        &:not(.is-disabled) {
            &:hover,
            &:focus-visible,
            &:active,
            &.is-active {
                color: $light-text;
                border-color: transparent;
            }
        }
    }

    &--secondary {
        border-color: $primary-border;

        &:not(:disabled),
        &:not(.is-disabled) {
            &:hover,
            &:focus-visible,
            &:active,
            &.is-active {
                color: $light-text;
                border-color: transparent;
            }
        }
    }

    &--secondary-light {
        @include style-button($light-text, transparent);
        border-color: $light-border;

        &:disabled,
        &.is-disabled {
            opacity: 1;
        }
    }

    &--link {
        width: auto;

        &:not(:disabled),
        &:not(.is-disabled) {
            &:hover,
            &:focus-visible,
            &:active,
            &.is-active {
                color: $light-text;
            }
        }
    }

    &--link-secondary,
    &--ghost {
        &:not(:disabled),
        &:not(.is-disabled) {
            &:hover,
            &:focus-visible {
                border-color: $primary-border;
                background-color: transparent;
            }

            &:active:not(.c-btn--toggle),
            &.is-active:not(.c-btn--toggle) {
                color: $light-text;
                border-color: transparent;
                background-color: $accent-dark-fill;
            }
        }
    }

    &--link-secondary {
        &.c-btn--toggle {
            &:not(:disabled),
            &:not(.is-disabled) {
                &.show,
                &.is-active {
                    border-color: $primary-border;
                }
            }
        }
    }

    &--ghost {
        font-weight: $font-weight-regular;

        &:not(:disabled),
        &:not(.is-disabled) {
            &:hover,
            &:focus-visible {
                box-shadow: $box-shadow-default;
            }
        }

        &.c-btn--toggle,
        &.show {
            &:not(:disabled),
            &:not(.is-disabled) {
                &.show,
                &.is-active {
                    color: $light-text;
                    background: $primary-fill;

                    &::after {
                        border-top-color: $light-border;
                    }

                    .c-btn__item {
                        color: $light-text;
                    }
                }
            }
        }
    }

    &--light {
        @include style-button($primary-text, $light-fill);
        box-shadow: $box-shadow-default;

        &:not(:disabled),
        &:not(.is-disabled) {
            &:hover,
            &:focus-visible {
                border-color: $primary-border;
                background-color: $light-fill;
            }

            &:active,
            &.is-active {
                color: $light-text;
                background-color: $accent-dark-fill;
                box-shadow: none;
            }
        }
    }

    &[data-hover-ico],
    &[data-active-ico] {
        padding-right: 42px;

        &::after {
            position: absolute;
            display: none;
            content: '';
            @include center-vertical-position;
            right: 14px;
        }

        &:not(:disabled),
        &:not(.is-disabled) {
            &:hover,
            &:focus-visible,
            &:active,
            &.is-active {
                &::after {
                    display: inline-block;
                }
            }
        }
    }
    &[data-hover-ico="pencil"] {
        &:not(:disabled),
        &:not(.is-disabled) {
            &:hover,
            &:focus-visible {
                &::after {
                    @include bg-svg($pencil);
                }
            }
        }
    }
    &[data-active-ico="pencil-light"] {
        &:not(:disabled),
        &:not(.is-disabled) {
            &:active,
            &.is-active {
                &::after {
                    @include bg-svg($pencil-light);
                }
            }
        }
    }

    &--md {
        padding: 11px 39px;
    }

    &--md-slim {
        padding-left: 15px;
        padding-right: 15px;

        .c-btn__ico-left {
            margin-left: -3px;
        }

        .c-btn__ico-right {
            margin-right: -3px;
        }
    }

    &--md-narrow {
        padding-left: 23px;
        padding-right: 23px;
    }

    &--xs,
    &--xs-narrow {
        padding: 8px 16px;

        .c-btn__ico-left {
            margin-right: 6px;
        }

        .c-btn__ico-right {
            margin-left: 6px;
        }
    }

    &--xs-narrow {
        padding-left: 7px;
        padding-right: 7px;

        &.c-btn--toggle {
            padding-right: 30px;

            &::after {
                margin-top: 1px;
            }
        }
    }

    &--xl {
        padding: 19px 39px;
    }

    &--toggle {
        padding-right: 38px;
        transition: .12s;

        &::after {
            display: inline-block;
            content: '';
            position: absolute;
            right: 14px;
            top: 50%;
            transform: translateY(-50%) rotate(0);
            transition: .5s;
            border-top: 5px solid;
            border-right: 5px solid transparent;
            border-bottom: 0;
            border-left: 5px solid transparent;
            border-top-color: $primary-border;
        }

        &.show,
        &.is-active {
            &::after {
                transform: translateY(-50%) rotate(180deg);
            }
        }
    }

    &--content-start,
    &--content-top-start {
        justify-content: flex-start;
        text-align: left;
    }

    &--content-top,
    &--content-top-start {
        align-items: flex-start;

        .c-btn__ico-left,
        .c-btn__ico-right {
            margin-top: 2px;
        }
    }

    &--content-center {
        justify-content: center;
        text-align: center;
    }
}


