.c-decorated-list {
    @include reset-list;

    & > * {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__item {
        display: flex;
        flex-wrap: nowrap;
        align-items: center;
        width: 100%;
        position: relative;
        z-index: 1;
        margin-bottom: 20px;
        min-height: 48px;

        & > .c-ico,
        & > .c-ico-back {
            position: absolute;
            @include center-vertical-position;
            left: 0;
        }
    }

    &__text {
        padding-left: 56px;
        color: $primary-text;
        line-height: $md-lh;
        border: none;
        outline: none;
        background: none;

        @include hover-focus {
            color: $primary-text;
        };
    }

    &--sm {
        & > * {
            margin-bottom: 16px;
        }

        .c-decorated-list__item {
            min-height: 36px;
        }
    }
}


